import { Component } from "react";
import { Row, Col, Form, ToggleButtonGroup, ToggleButton } from "react-bootstrap";
import Select from "react-select";
import ColorPicker from "../../CommonComponents/ColorPicker.js";
import { updateCardStyle, updateCtastatus } from "../../../../../actions/appsMoreAction.js";
import { connect } from "react-redux";
import InheritSwitch from './Element/InheritSwitch'
import RangeInput from "../../CommonComponents/RangeInput";
import { CARD_STYLE_ATHOR_DETAIL, CARD_STYLE_AUTHOR_COLOR, CARD_STYLE_CARD_COLOR, CARD_STYLE_CORNER_RADIUS, CARD_STYLE_FONT_COLOR, CARD_STYLE_FONT_LINK, CARD_STYLE_FONT_SIZE, CARD_STYLE_FONT_VARIENT, CARD_STYLE_HIDE_CONTENT, CARD_STYLE_HOVER_COLOR, CARD_STYLE_HOVER_EFFECT, CARD_STYLE_HOVER_TEXT, CARD_STYLE_POPUP_CONTENT, CARD_STYLE_POSITION, CARD_STYLE_REELS_VIEW, CARD_STYLE_SHARE_ACTION, CARD_STYLE_SHARE_OPTION, CARD_STYLE_SOCIAL_ICON_COLOR, CARD_STYLE_SOCIAL_ICON_COLOR_TYPE, CARD_STYLE_TIME, IS_CARD_STYLE_CHANGE } from "../../../../../reducer/keys.js";
import ErrorFound from "../../../../ErrorFound.js";
import SelectFont from "../Components/SelectFont.js";
import { postHighLightOptions } from "../../../../../utils/index.js";
class CardStyle extends Component {

  state = {
    spinner: false,
  };

  onChangeVariant = (e) => {
    this.props.CARD_STYLE_FONT_VARIENT(e.label.key)
  }

  onChangeHighlightPosition = (e) => {
    this.props.CARD_STYLE_POSITION(e.value)
  }

  componentDidUpdate(prevProps) {
    if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
      if (this.props.is_card_style_change) {
        setTimeout(() => {
          this.onSubmitData();
        }, 500);
      }
    }
  }

  onFontChange = (event) => {
    const { allFont, bannerDetail } = this.props
    const selectedFont = allFont.find(font => font.family == event.label.key)
    let selectVariant = JSON.parse(selectedFont.variants).find(item => item == bannerDetail.ThemeRule.font_varient)
    this.props.CARD_STYLE_FONT_LINK(selectedFont.family, selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
  }

  onSubmitData = (e) => {

    const { bannerDetail } = this.props;
    const data = {
      link_font: bannerDetail.ThemeRule.css_font === undefined ? '' : bannerDetail.ThemeRule.css_font.split(' ').join('+'),
      css_font: bannerDetail.ThemeRule.css_font === undefined ? '' : bannerDetail.ThemeRule.css_font,
      font_varient: bannerDetail.ThemeRule.font_varient === undefined ? '' : bannerDetail.ThemeRule.font_varient,
      cardColor: bannerDetail.ThemeRule.cardColor,
      fontColor: bannerDetail.ThemeRule.fontColor,
      fontSize: bannerDetail.ThemeRule.fontSize,
      roundEdge: bannerDetail.ThemeRule.roundEdge,
      authorColor: bannerDetail.ThemeRule.authorColor,
      iconType: bannerDetail.ThemeRule.iconType,
      iconColor: bannerDetail.ThemeRule.iconColor,
      postTime: Number(bannerDetail.Personalization.postTime),
      inheritStyles: Number(this.props.inheritStyle),
      postAuthor: Number(bannerDetail.Personalization.postAuthor),
      ctaStatus: Number(bannerDetail.Personalization.ctaStatus),
      shareOption: Number(bannerDetail.ThemeRule.shareOption),
      socialAction: Number(bannerDetail.ThemeRule.socialAction),
      hideContent: Number(bannerDetail.ThemeRule.hideContent),
      popupContent: Number(bannerDetail.ThemeRule.popupContent),
      themeRuleId: bannerDetail.ThemeRule.themeId,
      ownerId: localStorage.getItem('ownerId'),
      highlightPosition: bannerDetail.ThemeRule.highlightPosition,
      postHover: Number(bannerDetail.ThemeRule.postHover),
      shoppingText: bannerDetail.ThemeRule.shoppingText,
      showReelsView: bannerDetail.ThemeRule.showReelsView ? 1 : 0
    }

    updateCardStyle({ data, wallId: bannerDetail.Wall.wallId ? bannerDetail.Wall.wallId : localStorage.getItem('active_wall'), themeId: bannerDetail.ThemeRule.themeId, personalizationId: bannerDetail.Personalization.id }).then(res => {
      this.setState({ spinner: false })
    }).catch((error) => console.warn(error))

  }

  onHoverColorChange = (id) => {
    this.props.CARD_STYLE_HOVER_COLOR(id)
    this.props.CARD_STYLE_SOCIAL_ICON_COLOR(id ? "#ffffff" : "#000000")
    window.$(`#ThemeRuleIconColor`).minicolors('value', id ? "#ffffff" : "#000000")
    this.props.CARD_STYLE_FONT_COLOR(id ? "#ffffff" : "#000000")
    window.$(`#ThemeRuleFontColor1`).minicolors('value', id ? "#ffffff" : "#000000")
  }

  render() {
    const { inheritStyle, bannerDetail, is_card_style_change } = this.props;
    const { commerceTheme } = bannerDetail.Personalization
    return (<>
      <ErrorFound><InheritSwitch /></ErrorFound>
      <div className="mt-4">
        <p className="h5">General Style</p>
        <SelectFont inheritStyle={inheritStyle} fontChange={this.onFontChange} variantChange={this.onChangeVariant} fontval={bannerDetail.ThemeRule.css_font} vrntVal={bannerDetail.ThemeRule.font_varient} />
      </div>
      {[4, 16, 50, 53, 55, 19, 47, 61, 62].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="font_color">Font Color</Form.Label>
        <ColorPicker id="font_color" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={bannerDetail.ThemeRule.fontColor} changeColor={(clr) => this.props.CARD_STYLE_FONT_COLOR(clr)} colorChanged={is_card_style_change} inheritStyle={inheritStyle} />
      </div> : null}

      {[4, 16, 50, 53, 55, 19, 47, 61, 62, 3].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="font_size">Font Size</Form.Label>
        <RangeInput inheritStyle={inheritStyle} id="font_size" min={10} max={50} step={1} defaultValue={bannerDetail.ThemeRule.fontSize} value={bannerDetail.ThemeRule.fontSize} onChange={(event) => { this.props.CARD_STYLE_FONT_SIZE(event) }} />
      </div> : null}

      {[16, 53, 61, 62].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="radius_range">Card Radius</Form.Label>
        <RangeInput id="radius_range" min={0} max={50} step={1} defaultValue={bannerDetail.ThemeRule.roundEdge} value={bannerDetail.ThemeRule.roundEdge} onChange={(event) => { this.props.CARD_STYLE_CORNER_RADIUS(event) }} />
      </div> : null}

      {[3, 19, 47, 61, 62].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="card_color">Card Colors</Form.Label>
        <ColorPicker id="card_color" defaultColor={bannerDetail.ThemeRule.cardColor} changeColor={(clr) => { this.props.CARD_STYLE_CARD_COLOR(clr) }} colorChanged={is_card_style_change} />
      </div> : null}

      {[53].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="post_position">Position (Post Highlight)</Form.Label>
        <Select id="post_position" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={postHighLightOptions} value={postHighLightOptions.find(item => item.value == bannerDetail.ThemeRule.highlightPosition)} onChange={(e) => this.onChangeHighlightPosition(e)} />
      </div> : null}


      {[4, 16, 50, 53, 55, 61].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="hover_text">Hover Text</Form.Label>
        <Form.Control id="hover_text" value={bannerDetail.ThemeRule.shoppingText} maxLength="30" onChange={e => { this.props.CARD_STYLE_HOVER_TEXT(e.target.value) }} />
      </div> : null}

      {[16, 53, 61].includes(commerceTheme) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="hover_text">Hover Color</Form.Label>
        <Row>
          <Col>
            <div className={`border pt-3 ps-3 rounded-1 cursor-pointer overlfow-hidden ${bannerDetail.ThemeRule.postHover == 0 ? 'border-primary' : ''}`} onClick={(e) => { this.onHoverColorChange(0) }}>
              <div className='bg-white mt-3 ms-3 p-3 fw-medium fs-9' style={{ borderTopLeftRadius: 4, borderBottomRightRadius: 2 }}>White</div>
            </div>
          </Col>
          <Col>
            <div className={`border pt-3 ps-3 rounded-1 cursor-pointer overlfow-hidden ${bannerDetail.ThemeRule.postHover == 1 ? 'border-primary' : ''}`} onClick={(e) => { this.onHoverColorChange(1) }}>
              <div className='bg-dark mt-3 ms-3 p-3 fw-medium text-gray-100 fs-9' style={{ borderTopLeftRadius: 4, borderBottomRightRadius: 2 }}>Black</div>
            </div>
          </Col>
        </Row>
      </div> : null}
      {[16, 61].includes(commerceTheme) ?
        <div className="mt-4">
          <Form.Label className="mb-2" htmlFor="hover_effect">Hover Effect</Form.Label>
          <ToggleButtonGroup className="w-100 mb-3" type="radio" name="options" value={bannerDetail.ThemeRule.hideContent} onChange={(e) => { this.props.CARD_STYLE_HOVER_EFFECT(Number(e)) }}>
            <ToggleButton variant='secondary' id="h_e_caption" value={0}>
              Caption
            </ToggleButton>
            <ToggleButton variant='secondary' id="h_e_cta" value={1}>
              CTA
            </ToggleButton>
          </ToggleButtonGroup>
        </div> : null}

      <hr className=""></hr>


      <div>
        {!([61, 62].includes(commerceTheme)) ?
          <p className="h5">Author Style</p>
          : null}


        {[3, 19, 47].includes(commerceTheme) ? <div className="mt-4">
          <Form.Label className="mt-2" htmlFor="author_color">Author Color</Form.Label>
          <ColorPicker id="author_color" defaultColor={bannerDetail.ThemeRule.authorColor} changeColor={(clr) => { this.props.CARD_STYLE_AUTHOR_COLOR(clr) }} inheritStyle={inheritStyle} colorChanged={is_card_style_change} />
        </div> : null}

        {[3, 47, 19, 61, 16, 53, 4, 50, 55].includes(commerceTheme) ? <>
          <div className="mt-4">
            <Form.Check type="switch" id="default_icon_color" className="f-between mb-2" label="Default Social Icon Color" checked={bannerDetail.ThemeRule.iconType == 505 ? true : false} onChange={e => { this.props.CARD_STYLE_SOCIAL_ICON_COLOR_TYPE(e.target.checked ? 505 : 1) }} reverse />
          </div>
          <div style={{ display: bannerDetail.ThemeRule.iconType == 1 ? 'block' : 'none' }}>
            <ColorPicker id="icon_color" defaultColor={bannerDetail.ThemeRule.iconColor} changeColor={(clr) => { this.props.CARD_STYLE_SOCIAL_ICON_COLOR(clr) }} colorChanged={is_card_style_change} />
          </div>
        </> : null}

        {[3, 16, 19, 47, 53, 61, 62].includes(commerceTheme) ?
          <>
            {[3, 19, 47].includes(commerceTheme) ?
              <>
                <div className="mt-5">
                  <Form.Check type="switch" id="time_" className="f-between" label="Time" checked={bannerDetail.Personalization.postTime} onChange={(e) => { this.props.CARD_STYLE_TIME(Number(e.target.checked)) }} reverse />
                </div>
                <div className="mt-5">
                  <Form.Check type="switch" id="author_details" className="f-between" label="Author Detail" checked={bannerDetail.Personalization.postAuthor} onChange={(e) => { this.props.CARD_STYLE_ATHOR_DETAIL(Number(e.target.checked)) }} reverse />
                </div>
                <div className="mt-5">
                  <Form.Check type="switch" id="share_option" className="f-between" label="Share Options" checked={bannerDetail.ThemeRule.shareOption} onChange={(e) => { this.props.CARD_STYLE_SHARE_OPTION(Number(e.target.checked)) }} reverse />
                </div>
              </>
              : null}
            {[19, 47].includes(commerceTheme) ?
              <div className="mt-5">
                <Form.Check type="switch" id="hide_text" className="f-between" label="Hide Text" checked={bannerDetail.ThemeRule.hideContent} onChange={(e) => { this.props.CARD_STYLE_HIDE_CONTENT(Number(e.target.checked)) }} reverse />
              </div> : null}
          </> : null}
        <div className="mt-5">
          <Form.Check type="switch" id="social_actions" className="f-between" label="Social Actions" checked={bannerDetail.ThemeRule.socialAction} onChange={(e) => { this.props.CARD_STYLE_SHARE_ACTION(Number(e.target.checked)) }} reverse />
        </div>

        <div className="mt-5">
          <Form.Check type="switch" id="popup_content" className="f-between" label="Popup Content" checked={bannerDetail.ThemeRule.popupContent} onChange={(e) => { this.props.CARD_STYLE_POPUP_CONTENT(Number(e.target.checked)) }} reverse />
        </div>

        {[62, 61].includes(commerceTheme) ? <div className="mt-5">
          <Form.Check type="switch" id="reel_count" className="f-between" label="Reel View Count" checked={bannerDetail.ThemeRule.showReelsView} onChange={(e) => { this.props.CARD_STYLE_REELS_VIEW(Number(e.target.checked)) }} reverse />
        </div> : null}
      </div>

    </>
    );
  }
}


const stateToProps = (state) => {
  return {
    showCTAButton: state.appsMore?.detail?.wall?.User?.UserRule?.cta,
    videoPopup: state.appsMore.video_popup,
    is_card_style_change: state.appData?.is_card_style_change,
    inheritStyle: state.appData?.wall?.ThemeRule?.inheritStyles,
    allFont: state.allFont?.allFonts,
    appData: state.appData
  }
}

const DispatchToProps = dispatch => {
  return {
    updateCtastatus: (data) => dispatch(updateCtastatus(data)),
    CARD_STYLE_FONT_LINK: (font, varient) => { dispatch({ type: CARD_STYLE_FONT_LINK, font: font, varient: varient }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_FONT_VARIENT: (data) => { dispatch({ type: CARD_STYLE_FONT_VARIENT, varient: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_FONT_SIZE: (data) => { dispatch({ type: CARD_STYLE_FONT_SIZE, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_CARD_COLOR: (data) => { dispatch({ type: CARD_STYLE_CARD_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_FONT_COLOR: (data) => { dispatch({ type: CARD_STYLE_FONT_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HOVER_COLOR: (data) => { dispatch({ type: CARD_STYLE_HOVER_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HOVER_EFFECT: (data) => { dispatch({ type: CARD_STYLE_HOVER_EFFECT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HOVER_TEXT: (data) => { dispatch({ type: CARD_STYLE_HOVER_TEXT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_CORNER_RADIUS: (data) => { dispatch({ type: CARD_STYLE_CORNER_RADIUS, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_POSITION: (data) => { dispatch({ type: CARD_STYLE_POSITION, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },

    CARD_STYLE_AUTHOR_COLOR: (data) => { dispatch({ type: CARD_STYLE_AUTHOR_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_SOCIAL_ICON_COLOR_TYPE: (data) => { dispatch({ type: CARD_STYLE_SOCIAL_ICON_COLOR_TYPE, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_SOCIAL_ICON_COLOR: (data) => { dispatch({ type: CARD_STYLE_SOCIAL_ICON_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_TIME: (data) => { dispatch({ type: CARD_STYLE_TIME, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_ATHOR_DETAIL: (data) => { dispatch({ type: CARD_STYLE_ATHOR_DETAIL, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },

    CARD_STYLE_SHARE_OPTION: (data) => { dispatch({ type: CARD_STYLE_SHARE_OPTION, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_SHARE_ACTION: (data) => { dispatch({ type: CARD_STYLE_SHARE_ACTION, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HIDE_CONTENT: (data) => { dispatch({ type: CARD_STYLE_HIDE_CONTENT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_POPUP_CONTENT: (data) => { dispatch({ type: CARD_STYLE_POPUP_CONTENT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_REELS_VIEW: (data) => { dispatch({ type: CARD_STYLE_REELS_VIEW, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },

  }
}

export default connect(stateToProps, DispatchToProps)(CardStyle)