import moment from 'moment'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import Swal from 'sweetalert2'
import { Loader } from "../../../../../Layout/Main/CommonComponents/Loader";
import { DELETE_BIGCOMMERCE_PUBLISHED, GET_BIGCOMMERCE_PUBLISH_DATA, PUBLISH_BIGCOMMERCE_GALLERY } from '../../../../../../actions/bicommerceAction'
import EmbedCode from '../Components/EmbedCode'
import { Button, OverlayTrigger, Table, Tooltip, Tabs, Tab, InputGroup, Form, Col, Row } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import Select from 'react-select';


const ActionButton = ({ toolTip, clickAction, iconImg }) => {
    return <OverlayTrigger placement="top" overlay={<Tooltip>{toolTip}</Tooltip>}>
        <Button id={`action-${iconImg}`} variant="icon" className="btn-active-primary ms-2" onClick={event => clickAction()}>
            <FontAwesomeIcon icon={iconImg} />
        </Button>
    </OverlayTrigger>
}


const TopPositionSvg = () => <svg xmlns="http://www.w3.org/2000/svg" width="95" height="62" viewBox="0 0 95 62">
    <g id="Group_7039" data-name="Group 7039" transform="translate(-20 -290)">
        <rect id="Rectangle_2449" data-name="Rectangle 2449" width="95" height="10" rx="3" transform="translate(20 342)" fill="currentColor" opacity="0.15"></rect>
        <rect id="Rectangle_2452" data-name="Rectangle 2452" width="95" height="30" rx="3" transform="translate(20 290)" fill="currentColor" opacity="0.299"></rect>
        <rect id="Rectangle_2450" data-name="Rectangle 2450" width="95" height="10" rx="3" transform="translate(20 326)" fill="currentColor" opacity="0.15"></rect>
    </g>
</svg>

const BottomPositionSvg = () => <svg id='Group_7040' data-name='Group 7040' xmlns='http://www.w3.org/2000/svg' width='95' height='62' viewBox='0 0 95 62'>
    <rect id='Rectangle_2449' data-name='Rectangle 2449' width='95' height='10' rx='3' fill='currentColor' opacity='0.15'></rect>
    <rect id='Rectangle_2452' data-name='Rectangle 2452' width='95' height='30' rx='3' transform='translate(0 32)' fill='currentColor' opacity='0.299'></rect>
    <rect id='Rectangle_2450' data-name='Rectangle 2450' width='95' height='10' rx='3' transform='translate(0 16)' fill='currentColor' opacity='0.15'></rect>
</svg>
class BigCommercePublish extends Component {
    state = { showPage: true, showSectionBrand: false, showSectionCategory: false, theme: "", page: "", sectionBrand: "", sectionCategory: "", position: "top", wallId: localStorage.getItem('wallId'), themeData: false, pageData: false, sectionData: false, error: false, galleryName: '', defaultActiveKey: 'automatic_publishing', listView: false, categoryOptions: [], brands: [] }

    onLoad = () => {
        const { BigcommerceData } = this.props
        var storThemes = []
        var Pages = []
        var themeSection = []

        if (BigcommerceData && BigcommerceData.pagesArr) {
            // Object.entries(BigcommerceData.storThemes[0]).map(([key, value]) => ( storThemes.push({key:key, value:`${value.dawn},${value.id}`}) ))
            Object.entries(BigcommerceData.pagesArr).map(([key, value]) => (Pages.push({ key: value, value: `${key}` })))

            this.setState({ themeData: storThemes, pageData: Pages, theme: storThemes && storThemes.length ? storThemes[0].value : '', page: Pages && Pages.length ? Pages[0].value : '', section: BigcommerceData.themeSection && BigcommerceData.themeSection.length ? BigcommerceData.themeSection[0].section : '' })
            this.setState({
                listView: true
            })
        }
    }
    componentDidUpdate(prevProps) {
        const { BigcommerceData } = prevProps
        if (BigcommerceData != this.props.BigcommerceData) {
            this.onLoad()
            this.categoryFun()
        }
    }

    categoryFun() {
        const { BigcommerceData } = this.props
        if ((BigcommerceData && BigcommerceData.categories) && Object.keys(BigcommerceData.categories).length > 0) {
            this.setState({
                categoryOptions: Object.keys(BigcommerceData.categories).map(key => ({
                    value: key,
                    label: BigcommerceData.categories[key]
                }))
            })
        }
        if ((BigcommerceData && BigcommerceData.brands) && Object.keys(BigcommerceData.brands).length > 0) {
            this.setState({
                brands: Object.keys(BigcommerceData.brands).map(key => ({
                    value: key,
                    label: BigcommerceData.brands[key]
                }))
            })
        }
    }


    onChangePages = (item) => {
        const value = item.value
        if (value.includes('category_page')) this.setState({ page: value, showSectionCategory: true })
        else this.setState({ page: value, showSectionCategory: false })
        if (value.includes('brand_page')) this.setState({ page: value, showSectionBrand: true })
        else this.setState({ page: value, showSectionBrand: false })
    }

    publishGallery = () => {
        const { theme, page, position, wallId, showSectionBrand, showSectionCategory, galleryName, sectionCategory, sectionBrand } = this.state
        const { BigcommerceData } = this.props
        // if(!theme || !page || page.includes('HomePage') && !section){

        // if(!theme) this.setState({themeData:true}) 
        // if(!page) this.setState({pageData:true}) 
        // if(page.includes('HomePage') && !section) this.setState({pageData:true}) 
        // }else{
        // if(galleryName == '' || !galleryName){
        //     this.setState({error: true})
        //     return
        // }

        var obj = {
            page,
            wallId: localStorage.getItem('website_id'),
            gallery_name: this.getWebsiteName(),
            brand_id: showSectionBrand ? sectionBrand ? sectionBrand : Object.keys(BigcommerceData.brands)[0] : '',
            category_id: showSectionCategory ? sectionCategory ? sectionCategory : Object.keys(BigcommerceData.categories)[0] : '',
            position: position
        }
        this.props.PUBLISH_BIGCOMMERCE_GALLERY(obj)
        // }

    }

    deleteGallery = (id) => {

        Swal.fire({
            title: "Are you sure?",
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            cancelButtonText: "No",
        }).then((result) => {
            if (result.value) {
                var obj = new FormData()
                obj.append("id", id)
                this.props.DELETE_BIGCOMMERCE_PUBLISHED(id)
            }
            else if (result.dismiss === "cancel") { }
        });


    }
    getWebsiteName = () => {
        const { websiteList } = this.props

        const websiteId = localStorage.getItem('website_id');
        if (websiteList && websiteList.length) {
            const websiteName = websiteList.find(w => w.wall.Wall.id == websiteId)

            return websiteName.wall.Wall.name;
        }
        return ''
    }

    render() {
        const { BigcommerceData } = this.props
        const { theme, page, sectionCategory, sectionBrand, position, wallId, error, galleryName, showSectionCategory, showSectionBrand, themeData, pageData, sectionData, defaultActiveKey, listView, categoryOptions, brands } = this.state



        this.getWebsiteName();
        return (BigcommerceData ?
            <div className='afterPublish_screen'>
                <Tabs activeKey={defaultActiveKey} onSelect={event => this.setState({ defaultActiveKey: event })} id="justify-tab-example" className="mb-3">
                    <Tab eventKey="automatic_publishing" title="Automatic Publishing">
                        {BigcommerceData && BigcommerceData.embedData && BigcommerceData.embedData.length > 0 && listView ?
                            <div>
                                <div className='f-between flex-wrap'>
                                    <div>
                                        <h3 className="mb-1">Manage Published Galleries</h3>
                                        <p className="mb-2">Please Note: Manually installed plugins will not appear in this list</p>
                                    </div>
                                    <Button className='mb-2' onClick={() => this.setState({ listView: false })}>Add Automatic Publish</Button>
                                </div>
                                <Table responsive>
                                    <thead>
                                        <tr>
                                            <th>Page</th>
                                            <th>Position</th>
                                            <th>Date Installed</th>
                                            <th>Preview</th>
                                            <th className="text-end">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {BigcommerceData && BigcommerceData.embedData && BigcommerceData.embedData.length ? BigcommerceData.embedData.map((item, i) => {
                                            return <tr key={i}>
                                                <td>{item.pageTitle}</td>
                                                <td>{item.position}</td>
                                                <td className="text-nowrap">{moment(item.createdAt).format('MMMM DD, YYYY, hh:mm:ss A')} </td>
                                                <td> <Link to={`//${item.domain}`} className="btn btn-link" target="_blank">View<FontAwesomeIcon icon={'external-link'} iconClass={'ms-1'} /></Link></td>
                                                <td className="text-end">
                                                    <div className="d-flex align-items-center justify-content-end">

                                                        <ActionButton toolTip="Remove" clickAction={() => this.deleteGallery(item.id)} iconImg="trash-can" />
                                                    </div>
                                                </td>
                                            </tr>
                                        })
                                            :
                                            <tr><td colspan="5" className="text-center text-muted py-5 mt-4 border-bottom w-100">Data Not Found</td></tr>}
                                    </tbody>
                                </Table>
                            </div>
                            :
                            <div>
                                <h3 className='mb-1 h4'>Automatic Publishing </h3>
                                <p>From here you can publish your gallery directly onto your BigCommerce store.</p>
                                <Row className='mb-5'>
                                    <Form.Group as={Col} sm="6" className='mb-5'>
                                        <Form.Label>Website Name</Form.Label>
                                        <Form.Control size='lg' defaultValue={this.getWebsiteName()} readOnly />
                                    </Form.Group>
                                    <Form.Group as={Col} sm="6" className='mb-5'>
                                        <Form.Label>Select Your Landing Page (Loaction)</Form.Label>
                                        <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" options={pageData} getOptionLabel={option => option.key} onChange={this.onChangePages} />
                                    </Form.Group>
                                    {showSectionCategory ?
                                        <Form.Group as={Col} sm="6" className='mb-5'>
                                            <Form.Label>Choose The Category</Form.Label>
                                            <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" options={categoryOptions} onChange={item => this.setState({ sectionCategory: item.value })} />
                                        </Form.Group>
                                        : ""}
                                    {showSectionBrand ?
                                        <Form.Group as={Col} sm="6" className='mb-5'>
                                            <Form.Label>Choose The Brand</Form.Label>
                                            <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" options={brands} onChange={item => this.setState({ sectionBrand: item.value })} />
                                        </Form.Group>
                                        : ""}
                                    <Form.Group as={Col} sm="12" className='mb-5'>
                                        <h3 className='mb-4 h4'>Select The Position of Gallery on Page</h3>
                                        <Row style={{ maxWidth: 300 }}>
                                            <Col>
                                                <Button variant='secondary' className={`${position == 'top' ? 'active' : ''}`} onClick={() => this.setState({ position: 'top' })}>
                                                    <div className='f-center flex-column py-2'>
                                                        <span className='pb-3'>
                                                            <TopPositionSvg />
                                                        </span>
                                                        <p className='text-center fw-bold text-gray-500 mb-0'>Top</p>
                                                    </div>
                                                </Button>
                                            </Col>
                                            <Col>
                                                <Button variant='secondary' className={`${position == 'bottom' ? 'active' : ''}`} onClick={() => this.setState({ position: 'bottom' })} >
                                                    <div className='f-center flex-column py-2'>
                                                        <span className='pb-3'>
                                                            <BottomPositionSvg />
                                                        </span>
                                                        <p className='text-center fw-bold text-gray-500 mb-0'>Bottom</p>
                                                    </div>
                                                </Button>
                                            </Col>
                                        </Row>
                                    </Form.Group>
                                </Row>
                                <div className='border-top py-5 d-flex justify-content-end'>
                                    <Button variant='secondary' className='me-2' onClick={() => this.setState({ listView: true })}>Cancel</Button>
                                    <Button onClick={() => this.publishGallery()}>Publish Gallery</Button>
                                </div>
                            </div>
                        }
                    </Tab>
                    <Tab eventKey="manual_publishing" title="Manual Publishing">
                        <EmbedCode platform={2} galleryType={1} dataTag={1} allTags={1} count={1} />
                    </Tab>
                </Tabs>
            </div>
            :
            <Loader />
        )
    }
}

const mapStateToProps = (state) => {
    return {
        BigcommerceData: state.BigcommerceData.BigcommerceData,
        websiteList: state.websiteList.websiteList
    }
}
const mapDispatchToProps = dispatch => {
    return {
        PUBLISH_BIGCOMMERCE_GALLERY: (obj) => dispatch(PUBLISH_BIGCOMMERCE_GALLERY(obj)),
        DELETE_BIGCOMMERCE_PUBLISHED: (obj) => dispatch(DELETE_BIGCOMMERCE_PUBLISHED(obj)),
        GET_BIGCOMMERCE_PUBLISH_DATA: (obj) => dispatch(GET_BIGCOMMERCE_PUBLISH_DATA(obj))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(BigCommercePublish);