import { connect } from "react-redux";
import React, { Component } from 'react'
import { OverlayTrigger, Tooltip, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { CARD_STYLE_INHERIT, IS_CARD_STYLE_CHANGE, SETTING_CUSTOM_CSS } from '../../../../../../reducer/keys';

 class CssSwitch extends Component {
  render() {
    const {CssStatus} = this.props
    return <div className="f-between">
              <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`custom_css_status`}>
                  <span className='me-2'>Status</span>
                  <OverlayTrigger placement='top' overlay={<Tooltip>Add your Custom CSS to personalize the look of your Gallery.</Tooltip>} >
                      <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                  </OverlayTrigger>
              </Form.Label>
              <Form.Check type="switch" id={`custom_css_status`} defaultValue={CssStatus}  checked={CssStatus==1 ? true : false} onChange={(e) => { this.props.CARD_STYLE_INHERIT(Number(e.target.checked)) }} />
              </div>
  }
}

const stateToProps = (state) => {
    return {
        CssStatus:state.appData?.wall?.Personalization?.cssStatus
    }
  }
  const DispatchToProps = dispatch => {
    return {
         CARD_STYLE_INHERIT: (status)=> {dispatch({type:SETTING_CUSTOM_CSS, payload:Number(status)}); },
    }
  }

export default connect(stateToProps, DispatchToProps)(CssSwitch)