import Swal from "sweetalert2"
import { GET_SHOPIFY_DATA, SHOPIFY_LOADER_REQUESTING } from "../reducer/keys"
import { SHOPIFY_DELETE_PUBLISH, SHOPIFY_GET_PUBLISH, SHOPIFY_PUBLISH } from "./api"
import HttpClient from "./http-client"
import { TOAST_ERROR } from "../utils"

export const GET_SHOPIFY_PUBLISH_DATA = (isProduct) => {
    const obj = new FormData()
    obj.append('wallId', localStorage.getItem('active_wall'))
    if (isProduct) obj.append('type', "product")
    else obj.append('type', "other")
    return (dispatch) => {
        dispatch({
            type: SHOPIFY_LOADER_REQUESTING,
            payload: true,
        });
        new HttpClient().post(SHOPIFY_GET_PUBLISH, obj).then(res => {
            const { status, data } = res.data;
            dispatch({
                type: GET_SHOPIFY_DATA,
                payload: data,
            });
            setTimeout(() => dispatch({
                type: SHOPIFY_LOADER_REQUESTING,
                payload: false,
            }), 700)
        }).catch((error) => {
            console.log("ERROR", error)
        });
    }
}


export const DELETE_SHOPIFY_PUBLISHED = (id) => {
    return (dispatch) => {
        new HttpClient().post(SHOPIFY_DELETE_PUBLISH, { id })
            .then((response) => {
                const { status, data } = response.data;
                if (status) {
                    dispatch(GET_SHOPIFY_PUBLISH_DATA());
                    Swal.fire({
                        title: "Deleted!",
                        icon: "success",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                } else{
                    TOAST_ERROR("Something went wrong!")
                }
            }).catch((error) => {
                console.log("ERROR", error)
            });
    }
}

export const PUBLISH_SHOPIFY_GALLERY = (obj, callback = () => { } , galleryType) => {
    return (dispatch) => {
        new HttpClient().post(SHOPIFY_PUBLISH, obj)
            .then((response) => {
                const { status , message} = response.data;
                if (status) {
                    dispatch(GET_SHOPIFY_PUBLISH_DATA(galleryType));
                    Swal.fire({
                        title: "Publish successfully",
                        icon: "success",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                    callback()
                }
                else{
                    TOAST_ERROR("Something went wrong!")
                    callback()
                }

            }).catch((error) => {
                console.log("ERROR", error)

            });
    }
}