import { useEffect, useState } from 'react';
import moment from 'moment';
import { Alert, Row, Col, Table, Form, OverlayTrigger, Button, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import Select from 'react-select';

const ActionButton = ({ toolTip, clickAction, iconImg }) => {
       return <OverlayTrigger placement="top" overlay={<Tooltip>{toolTip}</Tooltip>}>
              <Button id={`action-${iconImg}`} variant="icon" className="btn-active-primary ms-2" onClick={event => clickAction()}>
                     <FontAwesomeIcon icon={iconImg} />
              </Button>
       </OverlayTrigger>
}
const AutomaticPublish = ({ ShopifyData, themeoption, showSection, themepageoption, galleryoptions, positionoption, publishGallery, onChangePages, onThemeChange, onSectionChange, deleteGallery, publishOption, onPositionChange, requestShopify, galleryType, pageData, page, themeSection, section, positions, position, loader }) => {
       const [showPublishing, setShowPublishing] = useState(!(ShopifyData && ShopifyData.embedData && ShopifyData.embedData.length > 0))
       const [loading, setLoading] = useState(false)

       const onPublished = () => {
              setLoading(false)
              setShowPublishing(false)
       }

       const onPublish = () => {
              setLoading(true)
              publishGallery(onPublished)
       }

       useEffect(()=>{
              if(!loader){
                     setShowPublishing(!(ShopifyData && ShopifyData.embedData && ShopifyData.embedData.length > 0))
              }
              
       },[ShopifyData, loader])
       return (
              (loader) ? 
              
              <Row>
                     <Col sm={8}>
                            <div className="sk_rect h-36px w-100 my-1"></div>
                     </Col>
                     <Col sm={4}>
                            <div className="sk_rect h-36px w-100 my-1"></div>
                     </Col>
              </Row>
              
              :
              <>
                     { !showPublishing ?
                     <>
                            <Row>
                                   <Col sm={8}>
                                          <h5 className="mb-3">Manage Published Galleries</h5>
                                          <Alert variant='warning' style={{ maxWidth: 991 }}>Please Note: Manually installed plugins will not appear in this list</Alert>
                                   </Col>
                                   <Col sm={4}>
                                          <div className='d-flex justify-content-center justify-content-lg-end'>
                                                 <Button onClick={() => setShowPublishing(true)}>Add Automatic Publishing</Button>
                                          </div>
                                   </Col>
                            </Row>
                            <Table responsive>
                                   <thead>
                                          <tr>
                                                 <th>Page</th>
                                                 <th>Position</th>
                                                 <th>Date Installed</th>
                                          </tr>
                                   </thead>
                                   <tbody>
                                          {ShopifyData && ShopifyData.embedData && ShopifyData.embedData.length ? ShopifyData.embedData.map((item, i) => {
                                                 return <tr key={i}>
                                                        <td>{item.pageTitle}</td>
                                                        <td>{item.position}</td>
                                                        <td className="text-nowrap">{moment(item.createdAt).format('MMMM DD, YYYY, hh:mm:ss A')} </td>
                                                        <td className="text-right">
                                                               <div className="d-flex align-items-center justify-content-end">
                                                                      <ActionButton toolTip="View" clickAction={()=>window.open(`//${item.domain}`)} iconImg="eye" />
                                                                      <ActionButton toolTip="Remove" clickAction={() => deleteGallery(item.id)} iconImg="trash-can" />
                                                               </div>
                                                        </td>
                                                 </tr>
                                          })
                                                 :

                                                 <tr><td colspan="3" className="text-center text-muted py-5 mt-4 border-bottom w-100" style={{ display: 'table-cell' }}>Data Not Found</td></tr>}
                                   </tbody>
                            </Table>
                            
                     </>
                     :
                     <>
                            <h5 className="">Automatic Publishing</h5>
                            <p className='fs-7 mb-5'>Just select your shopify page, theme, position of gallery, and then click on Publish Gallery.</p>
                            <Row className='mb-3'>
                            {galleryType == 1 ? <Col sm={6}>
                                          <Form.Group className='mb-3'>
                                                 <Form.Label> Select Your Shopify Landing Page </Form.Label>
                                                 { ShopifyData.pages ? <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" key={`${galleryType}_${publishOption}`} options={pageData} value={page} onChange={onChangePages} /> :
                                                        <p className='text-center fw-semibold text-muted mb-0 p-2'>No Store Found</p>}
                                          </Form.Group>
                                   </Col>:''}
                                   <Col sm={6}>
                                          <Form.Group className='mb-3'>
                                                 <Form.Label>Select Dawn Theme {galleryType == 2 ? `Product Page` : `Home Page`}  Section</Form.Label>
                                                 {requestShopify ?
                                                        <p className='sk_line sk_line_height_30 rounded sk_line_40 m-0 mb-3 sk_line_100'></p>
                                                        : themeSection && themeSection.length ? <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" key={`${themepageoption}_${galleryType}`} id={`themepageoption${publishOption}`} options={themeSection} defaultValue={section} onChange={onSectionChange} /> :
                                                        <p className='text-center fw-semibold text-muted mb-0 p-2'>No Store Found</p>}
                                          </Form.Group>
                                   </Col>
                                   <Col sm={6}>
                                          <Form.Group className='mb-3'>
                                                 <Form.Label>Position of Gallery on {galleryType == 2 ? `Product ` : ``} Page</Form.Label>
                                                 <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" key={`${positionoption}_${galleryType}`} id={`Position${galleryType}`} options={positions} onChange={onPositionChange} defaultValue={position} />
                                          </Form.Group>
                                   </Col>
                            </Row>
                            {ShopifyData && Object.keys(ShopifyData).length > 0 ? <div className='d-flex justify-content-end'>
                            <Button className={loading ? 'spinner' :''} onClick={onPublish}>Publish Gallery</Button>
                            </div>: null}
                     </>
                     }
              </>
       )
}

export default AutomaticPublish