export const addStyle = (id, url) => {
    if (!document.querySelector(`#style_font_${id}`)) {
        const link = document.createElement("link");
        link.href = url;
        link.rel = 'stylesheet';
        link.id = `style_font_${id}`
        document.head.appendChild(link)
    }
}
const HeadFont = ({ fonts }) => {
    return <>
        {fonts?.length > 0 && fonts.map((font, i) => addStyle(font.id, `https://fonts.googleapis.com/css?family=${font.family}:${JSON.parse(font.variants).join(',')}`))
        }
    </>
}

export default HeadFont