import React, { PureComponent } from "react";
import { Link } from "react-router-dom";

export default class Error203 extends PureComponent {
  onClicktoUrl = (path) => event => {
    window.top.location.href = path;
  }
  render() {
    const { wall } = this.props;
    return (
      <div className="ts_error_content">
        <div className="ts_error_title">Feed Not Activated!</div>
        <div className="ts_error_des">It seems you have not actived any Feed. Please Activate feed from
          <Link style={{ cursor: 'pointer' }} to={`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') :'' }`} className="manage-feed"> manage feed</Link> </div>
      </div>
    );
  }
}
