import React, { PureComponent } from "react";

export default class Error201 extends PureComponent {
  render() {    
    return (
      <div className="ts_error_content">
          <div className="ts_error_title">No Posts!</div>
          <div className="ts_error_des">If Moderation is ON, then the posts might be in the moderation section and you need to approve them by going to MODERATION.</div>
          <div className="ts_error_des">Else, there might not be any posts on feeds which you have added.</div>
      </div>
    );
  }
}
