import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Button, Image } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import EmbedCode from './EmbedCode'
import { baseUrl } from '../../../../../../Assets/AssetsHelper'
import TagBase from './TagBase'
import ShopifyPublish from '../ShopifyPublish/ShopifyPublish'
import BigCommercePublish from '../BigCommercePublish'
import { GET_BIGCOMMERCE_PUBLISH_DATA } from '../../../../../../actions/bicommerceAction'

export const NumberBullet = ({ count }) => <span className='d-inline-block text-center h-20px w-20px rounded-circle me-2 bg-primary text-gray-100 fs-9 flex-shrink-0' style={{ lineHeight: '20px' }}>{count}</span>
class PublishSteps extends Component {
    state = { searchData: [], isLoading: false, searchPost: null, openDropdwn: false, isPreview: false, dataTag: false, copied: false }
    componentDidMount() {
        const { isBigCommerce } = this.props
        if (isBigCommerce) {
            this.props.GET_BIGCOMMERCE_PUBLISH_DATA()
        }
    }


    render() {
        const { platform, galleryType, allTags, isShopify, isBigCommerce } = this.props
        const { dataTag } = this.state
        const usingTags = galleryType == 3 ? 'Using Tags' : '';
        var count = 0
        return (<div className='mb-10 pb-10'>
            {isShopify ?
                <ShopifyPublish galleryType={galleryType} allTags={allTags} />
                : <div>
                    {platform == 1 ?
                        <h5 className="mb-5">Publish On HTML Website {usingTags}</h5>
                        :
                        platform == 2 ?
                            <h5 className="mb-5">Publish Shoppable Gallery on Shopify {usingTags}</h5>
                            :
                            platform == 3 ?
                                <h5 className="mb-5">Publish On WordPress Website {usingTags}</h5>
                                :
                                platform == 4 ?
                                    <h5 className="mb-5">Publish On Prestashop Website {usingTags}</h5>
                                    :
                                    platform == 5 && !isBigCommerce ?
                                        <h5 className="mb-5">Publish Shoppable Gallery on Bigcommerce {usingTags}</h5>
                                        :
                                        platform == 6 ?
                                            <h5 className="mb-5">Publish On Magento Website {usingTags}</h5>
                                            :
                                            platform == 7 ?
                                                <h5 className="mb-5">Publish On Your Website {usingTags}</h5>
                                                :
                                                ''
                    }

                    {platform == 2 || (platform == 5 && !isBigCommerce) ?
                        <div className='px-lg-3'>
                            <div className='rounded-1 border d-flex p-lg-5 p-4 align-items-center mb-5'>
                                <div className='d-flex flex-grow-1 align-items-center'>
                                    <Image className='me-2' src={baseUrl('media/logo/tagshop-icon.svg')} height={50} width={50} fluid />
                                    <div className='d-flex flex-column'>
                                        <p className='h3 mb-1'>Tagshop App</p>
                                        <p className='mb-0'><Link id="instruction_link" target="_blank" to={platform == 2 ? 'https://tagshop.ai/support/taggbox-shopify-app/' : 'https://tagshop.ai/support/how-to-use-tagshop-bigcommerce-app/'} className='fw-medium'>Installation Instructions</Link></p>
                                    </div>
                                </div>
                                <div>
                                    <Link id="install_app" target="_blank" to={platform == 2 ? 'https://apps.shopify.com/instagram-shop?utm_source=Commerce&utm_medium=Editor&utm_campaign=Shopify-app' : 'https://www.bigcommerce.com/apps/taggshop/'} className='btn btn-primary'>Install App</Link>
                                </div>
                            </div>
                            <div className='f-center border-top my-10'>
                                <span className='h-40px w-40px fs-8 f-center fw-semibold rounded-circle border bg-body' style={{ marginTop: -20 }}>OR</span>
                            </div>
                        </div>
                        : null}

                    {platform == 2 ?
                        <h5 className="mb-5">Publish Gallery On Shopify Using HTML Code</h5>
                        :
                        platform == 5 ? isBigCommerce ? <BigCommercePublish galleryType={galleryType} allTags={allTags} /> :
                            <h5 className="mb-5">Publish Gallery On Bigcommerce Using HTML Code</h5>
                            :
                            ''
                    }

                    {galleryType == 3 ? <TagBase allTags={allTags} count={count + 1} updateData={(val) => this.setState({ dataTag: val })} /> : null}

                    {!(isBigCommerce && platform == 5) ? <div className='px-lg-3'>
                        <EmbedCode platform={platform} galleryType={galleryType} allTags={allTags} count={count + galleryType == 3 ? 2 : 1} dataTag={dataTag} />

                        {platform == 1 ?
                            <>
                                <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the publish code into the backend of the page</p>
                                <div className='ps-8'>
                                    <p>Simply log in to your <strong>HTML website</strong> and select the landing page where you wish to publish your gallery</p>
                                    <p>Paste the publish code into the backend of the page</p>
                                    <p>Apply the required changes and Voila! You will have successfully published the shoppable gallery on your website</p>
                                </div>
                            </>
                            : platform == 2 ?
                                <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the Publish code into the HTML of your shopify webpage</p>
                                : platform == 3 ?
                                    <>
                                        <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the publish code into the backend of the page</p>
                                        <div className='ps-8'>
                                            <p>Log in to your WordPress</p>
                                            <p>Select and edit the page where you want to publish the gallery and apply changes to display it on your website</p>
                                            <p>Click on (+) to add new block and choose custom HTML</p>
                                            <p>Paste the copied code</p>
                                            <p>Click on Publish/Update post and You're Done!</p>
                                        </div>
                                    </>
                                    : platform == 4 ?
                                        <>
                                            <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the publish code into the backend of the page</p>
                                            <div className='ps-8'>
                                                <p>Simply log in to your <strong>HTML website</strong> and select the landing page where you wish to publish your gallery</p>
                                                <p>Paste the publish code into the backend of the page</p>
                                                <p>Apply the required changes and Voila! You will have successfully published the shoppable gallery on your website</p>
                                            </div>
                                        </>
                                        : platform == 5 ?
                                            <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the Publish code into the HTML of your Bigcommerce webpage</p>
                                            : platform == 6 ?
                                                <>
                                                    <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the publish code into the backend of the page</p>
                                                    <div className='ps-8'>
                                                        <p>Log into your <strong>Magento account</strong></p>
                                                        <p>Navigate on the webpage in your Magento website where you want to showcase the Shoppable Gallery.</p>
                                                        <p>Paste the copied publish code and click on Apply to complete the process</p>
                                                        <p>Tadaa! you have now published the Shoppable gallery on Magento website.</p>
                                                    </div>
                                                </>
                                                : platform == 7 ?
                                                    <>
                                                        <p className="h6 mb-4"><NumberBullet count={count + galleryType != 1 ? 4 : 3} /> Paste the publish code into the backend of the page</p>
                                                        <div className='ps-8'>
                                                            <p>Here you can publish a Gallery on your website using either Javascript code or Iframe.</p>
                                                            <p>Copy the Generated code from tagshop Code Generator</p>
                                                            <p>You can also adjust the height and width of your Shoppable Gallery before Generating a code</p>
                                                            <p>Paste the Code on the backend of your website and save it.</p>
                                                            <p>Youre Done!</p>
                                                        </div>
                                                    </>
                                                    :
                                                    <>
                                                    </>
                        }

                    </div> : null}
                </div>}
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        BigcommerceData: state?.BigcommerceData?.BigcommerceData,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        GET_BIGCOMMERCE_PUBLISH_DATA: () => dispatch(GET_BIGCOMMERCE_PUBLISH_DATA())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(PublishSteps);