import React, { Component, Suspense } from 'react'
import { Container, Tab, Card, Button, Image, Nav } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { connect } from "react-redux";
import ContentSwitcher from '../CommonComponents/ContentSwitcher';
import GenerateCode from './GenerateCode';
import Theme from './Theme';
import PlatformAside from './GenerateCode/PlatformAside'
import CTAButtonStyle from './CTAButtonStyle'
import { GET_THEME_LIST, GET_WALL_DETAILS } from '../../../../actions/themeAction';
import Customization from './Customization';
import { GET_CTA_ACTION } from '../../../../actions/moderationActions';
import { getFontVariants } from '../../../../actions/appsMoreAction';
import { ACTIVE_WEBSITE_ID_REDUCER, IS_WALL_CHANGE, SAVE_AND_PUBLISH, SET_ALL_UPDATED } from '../../../../reducer/keys';
import { TOAST_SUCCESS } from '../../../../utils';
import { GET_ALL_WEBSITE_LIST, UPDATE_WEBSITE_WALL, CHANGE_WEBSITE, CHANGE_WEBSITE_WALL } from '../../../../actions/websiteAction';
import ErrorFound from '../../../ErrorFound';
import { Loader } from '../CommonComponents/Loader';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import { TOGGLE_ADD_WEBSITE } from '../../../../reducer/keys';
import { isWebsiteChanged } from '../../../../utils/checkForNavigation';
import { SNAPUP_DETAILS_ACTION } from '../../../../actions/addFeedActions';
import ThemeList from './Theme/ThemeList'
import CardAside from '../CommonComponents/CardAside';
import WebsiteSwitcher from '../CommonComponents/WebsiteSwitcher';
import withRouter from "../../../routes/WithRouter";
import "./website.scss"
import ErrorPage from './ErrorPage';
import ThemeConditionError from './ErrorPage/ThemeConditionError';
import EmptyWithIcon from '../CommonComponents/Empty/EmptyWithIcon';
import HeadFont from './Customization/HeadFont';
import Swal from 'sweetalert2';
import ThemePreview from "./GenerateCode/ThemePreview";
import SubHeader from './Components/SubHeader'
import { checkPermission } from '../../../../actions/collaboratorActions';
// import WebsiteSwitcher from './WebsiteSwitcher'; 


const isBigCommerce = window.location.pathname.includes("/bigcommerce/") ? true : false;
const CreateWebsite = ({ toggle, TOGGLE_ADD_WEBSITE }) => {
    return <div className="f-center py-4 w-100" style={{ height: 'calc(100vh - 95px)' }}>
        <div className="d-flex flex-column align-items-center">
            <Image data-mode="light" className="mb-5" src={baseUrl(`media/icons/color-icons/media.svg`)} height={120} width={120} alt={'media'} fluid/>
            <Image data-mode="dark" className="mb-5" src={baseUrl(`media/icons/color-icons/media-light.svg`)} height={120} width={120} alt={'media'} fluid/>
            <h4 className={`text-gray-dark fw-semibold text-center`}>Create a website, to start! </h4>
            <p className={"fs-7"}>Oops! Looks like you have deleted your website, create one by simply clicking on the button below.</p>
            <Button variant='primary' className="mb-2 btn-min-w" onClick={() => TOGGLE_ADD_WEBSITE()}>
                Create Website
            </Button>
        </div>
        <WebsiteSwitcher title="Website" addNew={toggle} noList={true} />
    </div>
}

const WebsiteLoader = () => <div style={{ minHeight: 'calc(100vh - 170px)' }}><Loader bg="body" /></div>

const NavOption = () => {
    return <Nav variant="underline" className='justify-content-center'>
        <Nav.Item className='ms-2'>
            <Nav.Link eventKey="theme">
                Theme
            </Nav.Link>
        </Nav.Item>
        <Nav.Item className='ms-2'>
            <Nav.Link eventKey="customization" disabled={!checkPermission('publish', 'website',true,true)}>
                Customization
            </Nav.Link>
        </Nav.Item>
        <Nav.Item className='ms-2' >
            <Nav.Link eventKey="cta" disabled={!checkPermission('publish', 'website',true,true)}>
                CTA Button
            </Nav.Link>
        </Nav.Item>
    </Nav>
}

class Website extends Component {
    constructor(props) {
        super(props);
        this.state = {
            footer_action1: false, footer_action2: true, footer_action3: false, galleryType: 1, banner: false, activeKey: '', activeGallery: [], changeSetting: false, spinner: false, mobilePrewiew: false, isWallChange: false, previewRerender: true, minNumberPost: 1, previewKey: 'script',
            width: '',//window.innerWidth, 
            isSavedList: false, selectedWall: '', platform: isBigCommerce ? 5 : 1, publishOption: 1, selectLoader: false, filterToggle: false
        };
        //this.handleWindowResize = this.handleWindowResize.bind(this);
    }

    componentDidMount() {
        const { websiteData, ctaData, themeData } = this.props
        if (!(websiteData?.websiteList && websiteData?.websiteList.length)) this.props.GET_ALL_WEBSITE_LIST()
        if (themeData && themeData.themeList.length <= 0) this.props.GET_THEME_LIST();

        this.props.getFontVariants();
        if (websiteData?.websiteList && websiteData?.websiteList.length) {
            if (localStorage.getItem('active_wall') && localStorage.getItem('active_wall') > 0) {
                const { allWalls } = this.props
                var gallery = allWalls?.find((item) => item.id == localStorage.getItem('active_wall'));
                gallery = gallery ? gallery : allWalls && allWalls.length > 0 ? allWalls[0] : null
                if (!ctaData) this.props.GET_CTA_ACTION(this.props.appData?.wall);
                this.setState({ activeGallery: gallery })
                if (gallery?.id && this.props.appData?.wall?.Personalization?.commerceTheme) { this.props.CHANGE_WEBSITE_WALL(gallery?.id, this.props.appData?.wall?.Personalization.numberOfPosts ? this.props.appData?.wall?.Personalization.numberOfPosts : 20, this.props.appData?.wall?.Personalization?.commerceTheme) }
            } else {
                let firstWallId = this.props.allWalls && this.props.allWalls.length ? this.props.allWalls[0] : ''
                // localStorage.setItem('active_wall' ,firstWallId?.id ) 
                this.props.CHANGE_WEBSITE_WALL(firstWallId?.id, 20, 61)
                this.setState({ activeGallery: firstWallId })
            }
        }

        window.addEventListener('resize', this.handleWindowResize);
        this.selectedWebsite()
    }

    changeGalleryType = (e) => {
        this.setState({ galleryType: e, previewKey: 'script' })
        // this.changeActiveTab()
    }
    changePlatform = e => {
        this.setState({
            platform: e
        })
    }

    selectedWebsite = () => {
        const { websiteList } = this.props.websiteData
        var webData = websiteList?.find((x) => x.wall.Wall.id == localStorage.getItem('website_id'));
        this.setState({ selectedWall: webData && webData?.wall ? webData : websiteList && websiteList[0] ? websiteList[0] : '' })
    }

    ChangeGallery = (id, noChange) => {
        const { allWalls } = this.props
        var gallery = allWalls?.find((item) => item.id == id);
        this.setState({ activeGallery: gallery, selectLoader: false })
        if (!noChange) this.props.WALL_CHANGED()
        else localStorage.setItem("active_wall", gallery?.id)
        if (this.props.appData?.wall?.ThemeRule.themeId || noChange) {
            this.props.CHANGE_WEBSITE_WALL( gallery?.id, this.props.appData?.wall?.ThemeRule.numberOfPosts ? this.props.appData?.wall?.ThemeRule.numberOfPosts : 20, this.props.appData?.wall ? this.props.appData?.wall?.ThemeRule.themeId : 62)
        }
    }

    changeActiveTab = () => {
        var targets = document.querySelectorAll('.publishTab');
        targets.forEach(function (target) {
            if (target.classList.contains('active')) {
                target.classList.remove('active');
            }
        });
        document.querySelector('#html-tab').classList.add('active');
    }

    componentDidUpdate(prevProps) {
        const { websiteData, allWalls, appData } = this.props
        if (websiteData?.websiteList.length != prevProps.websiteData?.websiteList.length) {
            if (websiteData?.websiteList.length > 0) {

                let wall = null
                let webD = this.props.websiteData?.websiteList?.find((x) => x.wall?.Wall?.id == localStorage.getItem('website_id'))
                webD = webD ? webD : this.props.websiteData?.websiteList[0]
                if (allWalls && allWalls.length > 0) {
                    wall = allWalls.find(item => item.id == webD?.wall?.Wall?.wallId)
                    wall = wall ? wall : allWalls[0]
                }
                //this.changeWebsite(webD.wall.Wall)
                this.setState({ selectedWall: webD, activeGallery: wall, footer_action1: false, footer_action2: true, footer_action3: false }, () => wall ? this.ChangeGallery(wall.id, 'noChange') : {})
                this.props.GET_APP_DATA(webD)
                localStorage.setItem('website_id', webD.wall.Wall.id);
            }
            else {
                this.setState({ selectedWall: null, activeGallery: allWalls && allWalls.length > 0 ? allWalls[0] : null, footer_action1: false, footer_action2: true, footer_action3: false }, () => {
                    if (allWalls & allWalls.length > 0) this.ChangeGallery(allWalls[0].id, 'noChanges')
                })
                localStorage.setItem('website_id', '');
            }

        }
        if (prevProps.allWalls?.length != allWalls?.length && this.state.selectedWall) {
            const wall = allWalls && allWalls.length > 0 ? allWalls.find(item => item.id == this.state.selectedWall.wall.Wall.wallId) : null
            this.ChangeGallery(wall ? wall.id : allWalls && allWalls.length > 0 ? allWalls[0].id : null, 'noChanges')
        }

        if (appData?.live_preview_loader && appData?.isPostLoading && !prevProps.appData?.live_preview_loader && !prevProps.appData?.isPostLoading) this.setState({ previewRerender: false })

        // if (this.props.websiteData?.activeWebsite_id !== prevProps.websiteData?.activeWebsite_id) {
        //     var websiteD = this.props.websiteData?.websiteList?.find((x) => x.wall?.Wall?.id == localStorage.getItem('website_id'));
        //     if (websiteD) {
        //         this.props.GET_APP_DATA(websiteD);
        //         if (websiteD?.wall?.Wall?.wallId) {
        //             this.ChangeGallery(websiteD?.wall?.Wall?.wallId)
        //         }
        //         else {
        //             localStorage.setItem('active_wall', this.props.allWalls && this.props.allWalls[0] ? this.props.allWalls[0].id : '')
        //             this.setState({ activeGallery: this.props.allWalls && this.props.allWalls[0] ? this.props.allWalls[0] : '' })
        //             this.ChangeGallery(this.props.allWalls && this.props.allWalls[0] ? this.props.allWalls[0].id : '')
        //         }
        //     }
        // }

        // if (this.props.appData?.wall?.Personalization?.commerceTheme !== prevProps.appData?.wall?.Personalization?.commerceTheme) {
        //     this.setState({ changeSetting: true }, () => {
        //         setTimeout(() => {
        //             this.setState({ changeSetting: false })
        //         }, 500)
        //     });
        // }

        // if (this.props.allWalls !== prevProps.allWalls) {
        //     var gallery = this.props.allWalls?.find((item) => item.id == localStorage.getItem('active_wall'));
        //     this.setState({ activeGallery: gallery })
        // }
    }


    applyWebsite_wall = () => {
        const {activeGallery} = this.state
        const obj = { wallId: activeGallery ? activeGallery.id : localStorage.getItem('active_wall'), id: localStorage.getItem('website_id') }
        this.setState({ spinner: true })
        setTimeout(() => {
            UPDATE_WEBSITE_WALL(obj).then(res => {
                const { responseCode, responseMessage } = res.data
                this.setState({ spinner: false, isWallChange: false })
                if (responseCode == 200) {
                    TOAST_SUCCESS(responseMessage);
                    this.props.GET_ALL_WEBSITE_LIST()
                    this.props.SET_ALL_UPDATED();
                } else { }
            }).catch((error) => console.warn(error))
        }, 1000);
    }

    discardSetting = () => {
        var webData = this.props.websiteData?.websiteList?.find((x) => x.wall.Wall?.id == localStorage.getItem('website_id'));
        const element = document.querySelector('#discardSetting')
        if (webData) {
            if (!element.getAttribute('data-nav')) {
                Swal.fire({
                    title: "Are you sure?",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonText: "Discard",
                    cancelButtonText: "Cancel",
                }).then(res => {
                    if (res.isConfirmed) {
                        this.props.GET_APP_DATA(webData);
                    }
                })
            }
            else this.props.GET_APP_DATA(webData)
            //      this.ChangeGallery(webData?.wall?.Wall?.wallId ? webData?.wall?.Wall?.wallId : this.props.allWalls && this.props.allWalls[0] ? this.props.allWalls[0].id : '')
        }

    }

    closeAside = () => this.setState({ filterToggle: false })

    onGenerateCode = () => {
        const { websiteList } = this.props.websiteData
        const currWebsite = localStorage.getItem("website_id")
        const wallDetails = websiteList && websiteList.length ? websiteList?.find(x => x.wall.Wall?.id == currWebsite) : null
        if (wallDetails) {
            if (!wallDetails.wall.Wall?.wallId || wallDetails.wall.Wall?.wallId == 0) {
                this.props.SAVE_AND_PUBLISH(true);
                this.applyWebsite_wall()
                setTimeout(() => {
                    isWebsiteChanged(() => this.setState({ footer_action2: false, footer_action1: true, footer_action3: true }))
                }, 500);
            }
            else {
                isWebsiteChanged(() => this.setState({ footer_action2: false, footer_action1: true, footer_action3: true }))
            }
        }
        else isWebsiteChanged(() => this.setState({ footer_action2: false, footer_action1: true, footer_action3: true }))
        

        const reviewHub = document.getElementById("subtree-container");
        if (reviewHub) {
            reviewHub.remove(); 
        }
    }

    changeWebsite = (item) => {
        const { websiteData, navigate, allWalls } = this.props
        isWebsiteChanged(() => {
            this.setState({ selectedWall: null, selectLoader: true, activeGallery: null })
            localStorage.setItem('website_id', item.id);
            window.location.pathname.includes("/shopify/") ? navigate(`/website/shopify/${localStorage.getItem('website_id')}`) :
                window.location.pathname.includes("/bigcommerce/") ? navigate(`/website/bigcommerce/${localStorage.getItem('website_id')}`) :
                    navigate(`/website/publish/${item.id}`)
            var webData = websiteData?.websiteList?.find((x) => x.wall.Wall.id == item.id);
            const wall = allWalls && allWalls.length > 0 ? allWalls.find(item => item.id == webData.wall.Wall.wallId) : null
            this.setState({ selectedWall: webData, activeGallery: wall, selectLoader: false }, () => this.ChangeGallery(wall ? wall.id : allWalls && allWalls.length > 0 ? allWalls[0].id : {}))
            this.props.GET_APP_DATA(webData);
            this.props.ACTIVE_WEBSITE_ID();
        })
    }



    render() {
        const { footer_action1, footer_action2, footer_action3, publishOption, activeKey, activeGallery, changeSetting, isWallChange, previewRerender, width, selectedWall, selectLoader, platform, galleryType, previewKey, filterToggle } = this.state;
        const { themeData, allWalls, appData, websiteData, allFont, authorized } = this.props
        const authData = authorized?.userData?.user_data?.sessionData
        const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'

        var isShopify = window.location.pathname.includes("/shopify/") ? true : false;

        return <div className='d-flex flex-column flex-column-fluid full-content'>
            <SubHeader websiteUrl={websiteUrl} />
            <Container fluid>
                <HeadFont fonts={allFont} />
                {websiteData?.websiteList?.length ?
                    <Card className="content_inner website__ border-0">
                        <Card.Body className='p-0 d-flex w-100'>
                            <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                                <Card className='a_header_ a_footer_'>
                                    <Card.Header className='py-4'>
                                        <ErrorFound> <WebsiteSwitcher contentList={websiteData?.websiteList} changeWebsite={this.changeWebsite} selectedContent={selectedWall?.wall?.Wall} title="Website" isWebsite={true} /></ErrorFound>
                                    </Card.Header>
                                    <Card.Body className='bg-gray-100 aside_body p-0' style={{ marginBottom: -1 }}>
                                        {!footer_action1 ?
                                            <div className='border-bottom p-4'>
                                                <h5>Select Content</h5>
                                                <ErrorFound> <ContentSwitcher contentList={allWalls} modifyContent={(data) => this.ChangeGallery(data.id)} selectedContent={activeGallery} title="Website" onlySwitcher={true} selectLoader={selectLoader} /></ErrorFound>
                                            </div>
                                            : ''}
                                        {footer_action1 ?
                                            <PlatformAside changeGalleryType={this.changeGalleryType} galleryType={galleryType} changePlatform={this.changePlatform} platform={platform} isShopify={isShopify} />
                                            : ''}
                                        {footer_action2 ?
                                            <Tab.Container defaultActiveKey="theme">
                                                <NavOption />
                                                <Tab.Content>
                                                    <Tab.Pane eventKey="theme">
                                                        <ErrorFound><ThemeList themeList={themeData.themeList} refreshEmbed={this.refreshEmbed} /></ErrorFound>
                                                    </Tab.Pane>
                                                    <Tab.Pane eventKey="customization">
                                                        {appData?.wall && !changeSetting && <ErrorFound><Customization a_key={activeKey} bannerDetail={this.props?.bannerDetail} appData={this.props?.appData} fonts={this.props?.fonts} /></ErrorFound>}
                                                    </Tab.Pane>
                                                    <Tab.Pane eventKey="cta">
                                                        {appData?.wall ? <ErrorFound><CTAButtonStyle /></ErrorFound> : ''}
                                                    </Tab.Pane>
                                                </Tab.Content>
                                            </Tab.Container> : ''}
                                    </Card.Body>
                                    <Card.Footer className='d-flex justify-content-end'>
                                        {footer_action2 ? (appData?.is_wall_change || appData?.is_themes_change || appData?.is_backgroun_change || appData?.is_banner_IMG_change || appData?.is_banner_change || appData?.is_card_style_change || appData?.is_theme_setting_change || appData?.is_custome_css_change || appData?.is_magic_upload_change || appData?.is_cta_setting_change || appData?.is_utm_tracker_change) ?
                                            <div className="f-between w-100">
                                                <Button variant='link text-nowarp' id='discardSetting' onClick={() => this.discardSetting()}>
                                                    <FontAwesomeIcon icon={'arrow-left'} iconClass="me-1" />
                                                    Discard
                                                </Button>
                                                <Button id='saveSetting' className={`ms-3 text-nowarp ${this.state.spinner ? 'spinner' : ''}`} onClick={() => { this.props.SAVE_AND_PUBLISH(true); this.applyWebsite_wall() }}>
                                                    Save Changes
                                                </Button>
                                            </div>
                                            : null
                                            : ''}
                                        {footer_action3 ?
                                            <div className='my-1'>
                                                <Button className='ms-3' onClick={() => this.setState({ footer_action1: false, footer_action2: true, footer_action3: false })}>
                                                    <FontAwesomeIcon icon={'pen'} iconClass="me-1" />
                                                    Edit
                                                </Button>
                                            </div>
                                            : ''}
                                    </Card.Footer>
                                </Card>
                            </CardAside>

                            <Card className={`preview_ ${footer_action1 ? 'p_header_' : 'p_footer_'}`}>
                                <Button id="toggle_aside" variant='primary' className='f-center d-xl-none position-absolute end-0 top-0 rounded-pill m-3' onClick={() => this.setState({ filterToggle: !filterToggle })} style={{ zIndex: 9 }}>
                                    <FontAwesomeIcon icon={'sliders'} iconClass="me-1" />
                                    Menu
                                </Button>
                                {footer_action1 ?
                                    <Tab.Container activeKey={previewKey} onSelect={event => this.setState({ previewKey: event })}>
                                        <Card.Header className='py-0 align-items-end'>
                                            <Nav variant="underline" className='justify-content-center'>
                                                <Nav.Item className='me-2'>
                                                    <Nav.Link eventKey="script">
                                                        Script
                                                    </Nav.Link>
                                                </Nav.Item>
                                                <Nav.Item className='me-2'>
                                                    <Nav.Link eventKey="preview">
                                                        Preview
                                                    </Nav.Link>
                                                </Nav.Item>
                                            </Nav>
                                        </Card.Header>
                                        <Card.Body>
                                            <Tab.Content>
                                                <Tab.Pane eventKey="script">
                                                    <ErrorFound>
                                                        <GenerateCode key={localStorage.getItem('website_id')} galleryType={galleryType} platform={platform} closePopup={this.closePopup} isShopify={isShopify} isBigCommerce={isBigCommerce} />
                                                    </ErrorFound>
                                                </Tab.Pane>
                                                <Tab.Pane eventKey="preview">
                                                    <div className='theme_preview_wrap'>
                                                        <Suspense fallback={<></>}>
                                                            <ErrorFound><ThemePreview key={previewKey} /></ErrorFound>
                                                        </Suspense>
                                                    </div>
                                                </Tab.Pane>
                                            </Tab.Content>
                                        </Card.Body>
                                    </Tab.Container>
                                    : ''}
                                {footer_action2 ? <>
                                    <Card.Body className='position-relative'>
                                        {appData?.live_preview_loader || appData?.isPostLoading ? <Loader bg={'body'} /> : ''}
                                        <ErrorFound><Theme webId={localStorage.getItem('website_id') ? localStorage.getItem('website_id') : websiteData?.websiteList.length > 0 ? websiteData?.websiteList[0].wall.Wall.id : ''} /></ErrorFound>
                                        {!appData?.live_preview_loader && !appData?.isPostLoading && !previewRerender ? <>
                                            {appData?.error_code && appData?.error_code > 0 ? <ErrorFound><ErrorPage errorCode={appData?.error_code} wall={appData?.wall} /></ErrorFound> : <ErrorFound><ThemeConditionError appData={appData} /></ErrorFound>}
                                        </> : null}
                                        {allWalls && allWalls.length <= 0 && !appData?.live_preview_loader && !appData?.isPostLoading && appData?.error_code <= 0 ?
                                            <div className="f-center h-100">
                                                <EmptyWithIcon title={'Preview Missing? '} desc={'You skipped the preliminary step. Connect your social account to collect content and begin creating your gallery.'} icon={'media'} button={{ action: () => this.props.navigate(`/content/moderation/${localStorage.getItem('active_wall')}`), text: 'Add Content' }} isFullScreen={false} />
                                            </div>
                                            : null}
                                    </Card.Body>
                                    <Card.Footer>
                                        <div className='f-center justify-content-end'>
                                            <Button variant='primary' className='mx-4 my-2' onClick={this.onGenerateCode}>
                                                <FontAwesomeIcon icon={'bolt'} iconClass="me-1" />
                                                Generate Code
                                            </Button>
                                        </div>
                                    </Card.Footer>
                                </>
                                    : ''}
                            </Card>
                        </Card.Body>
                    </Card>
                    :
                    websiteData?.websiteLoading ? <WebsiteLoader />
                        : <CreateWebsite toggle={this.props.toggle} TOGGLE_ADD_WEBSITE={this.props.TOGGLE_ADD_WEBSITE} />
                }
            </Container>
        </div>
    }
}


const mapStateToProps = (state) => {
    return {
        authorized: state.authorized,
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        bannerDetail: state.appsMore,
        fonts: state.appsMore.fonts,
        themeData: state.themeData,
        ctaData: state.ctaData.data,
        allFont: state.allFont?.allFonts,
        websiteData: state.websiteList,
        appData: state.appData,
        toggle: state.websiteList.addWebsiteNow,
    }
}
const mapDispatchToProps = dispatch => {

    return {
        GET_WALL_DETAILS: () => dispatch(GET_WALL_DETAILS()),
        GET_THEME_LIST: () => dispatch(GET_THEME_LIST()),
        getFontVariants: () => dispatch(getFontVariants()),
        GET_CTA_ACTION: (wallData) => dispatch(GET_CTA_ACTION(wallData)),
        GET_APP_DATA: (data) => dispatch(CHANGE_WEBSITE(data)),
        CHANGE_WEBSITE_WALL: (Id, limit, themeId) => dispatch(CHANGE_WEBSITE_WALL(Id, limit, themeId)),
        SAVE_AND_PUBLISH: (status) => dispatch({ type: SAVE_AND_PUBLISH, payload: status }),
        SET_ALL_UPDATED: () => dispatch({ type: SET_ALL_UPDATED, payload: false }),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
        TOGGLE_ADD_WEBSITE: () => dispatch({ type: TOGGLE_ADD_WEBSITE, payload: true }),
        GET_ALL_WEBSITE_LIST: () => dispatch(GET_ALL_WEBSITE_LIST()),
        SNAPUP_DETAILS_ACTION: () => dispatch(SNAPUP_DETAILS_ACTION()),
        ACTIVE_WEBSITE_ID: () => dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: localStorage.getItem('website_id') }),
        WALL_CHANGED: () => dispatch({ type: IS_WALL_CHANGE, payload: true })


    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Website));