import React, { PureComponent } from "react";
import { baseUrl } from "../../../../../Assets/AssetsHelper";

export default class ThemeError extends PureComponent {


    render() {
        const { appData } = this.props  
        const themeId = appData?.wall?.Personalization?.commerceTheme;
        const postNumber = appData?.post_data && appData?.post_data.length ? appData?.post_data.length : appData?.custom_post.length? appData?.custom_post.length : 0;
        let errorShow = false;
        let minNumberPost = 1;
        
        errorShow = themeId === 16 && postNumber < appData?.wall?.ThemeRule?.multiRow ? true :
                    themeId === 53 && postNumber < appData?.wall?.ThemeRule?.numberOfCoumn ? true :
                    themeId === 4 && appData?.wall?.Personalization.fitRow && (postNumber < appData?.wall?.ThemeRule?.numberOfCoumn) ? true :
                    themeId === 50 && postNumber < 3 ? true :
                    themeId === 61 && postNumber < 1 ? true :
                    themeId === 3 && postNumber < 1 ? true :
                    themeId === 19 && postNumber < 1 ? true :
                    themeId === 47 && postNumber < 1 ? true :
                    themeId === 55 && postNumber < 1 ? true :
                    false;
        
        minNumberPost = themeId === 16 && postNumber < appData?.wall?.ThemeRule?.multiRow ? appData?.wall?.ThemeRule?.multiRow :
                        themeId === 53 && postNumber < appData?.wall?.ThemeRule?.numberOfCoumn ? appData?.wall?.ThemeRule?.numberOfCoumn :
                        themeId === 4 && appData?.wall?.Personalization.fitRow && (postNumber < appData?.wall?.ThemeRule?.numberOfCoumn) ? appData?.wall?.ThemeRule?.numberOfCoumn :
                        themeId === 50 && postNumber < 3 ? 3 :
                        themeId === 61 && postNumber < 1 ? 1 :
                        themeId === 3 && postNumber < 1 ? 1 :
                        themeId === 19 && postNumber < 1 ? 1 :
                        themeId === 47 && postNumber < 1 ? 1 :
                        themeId === 55 && postNumber < 1 ? 1 :
                        1;

        const ErrorImage = { backgroundImage: `url(${baseUrl('media/images/error/nopost.png')})` }
        const LogoImage = { backgroundImage: `url(${baseUrl('media/images/logo/tagshop-logo.svg')})` }
        return (errorShow ?
            <div className="ts_error_page">
                <div className="ts_error_page_bg" style={ErrorImage}><div></div></div>
                <div className="ts_error_page_wrapper">
                    <div className="ts_error_content">
                        <div className="ts_error_title">Error: Theme Conditions Not Met</div>
                        { appData?.wall?.Personalization?.commerceTheme != 61 ? <div className="ts_error_des">Your shoppable gallery isn't visible. You need at least "{minNumberPost}" public posts to see the preview as per the theme rule.</div> : <></>}
                        <div className="ts_error_des"><strong>Note: </strong>{ appData?.wall?.Personalization?.commerceTheme == 61 ? 'Please make sure your public posts are in reels/videos & image format as this theme does not support posts in text format' : 'Please make sure your public posts are in image & video format as this theme does not support posts in text format.'}</div>
                        <div className="ts_error_page_actions">
                            <div className="ts_error_action_list">
                            </div>
                        </div>
                    </div>

                    <div className="ts_error_page_logo">
                        <span className="ts_error_page_logo_ico" style={LogoImage}></span>
                    </div>
                </div>
            </div>:''
        );
    }
}