import React, { Component } from 'react'
import { Dropdown, Image, ToggleButtonGroup, ToggleButton, Button } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';
export default class PlatformAside extends Component {
    render() {
        const { galleryType, changeGalleryType, changePlatform, platform, isShopify } = this.props

        const galleryTypes = [{
            title: 'Homepage & Landing Page',
            desc: 'To publish your shoppable gallery on your online stores homepage, category page, inspiration page, lookbooks, or any other pages.',
            icon: 'browser',
            color: '#9496C8',
            value: 1
        },
        {
            title: 'Product Page Galleries',
            desc: 'To publish the shoppable gallery on your online stores product pages where posts will be filtered based on each Product ID.',
            icon: 'box',
            color: '#7DC0AE',
            value: 2
        },
        {
            title: 'Tag-based Shoppable Gallery',
            desc: 'Filter your gallery by Tags and publish the tag-based gallery anywhere on your online store.',
            icon: 'image',
            color: '#FF9E9E',
            value: 3
        }]
        const platforms = [{ name: 'html', value: 1 },
        { name: 'shopify', value: 2 },
        { name: 'wordpress', value: 3 },
        { name: 'prestashop', value: 4 },
        { name: 'big-commerce', value: 5 },
        { name: 'magento', value: 6 },
        { name: 'other', value: 7 }]
        return (
            <>
                <div className='p-4'>
                    <h5>Gallery Type</h5>

                    {isShopify ? <ToggleButtonGroup type="radio" name="galleryType" defaultValue={galleryType} className="mb-2 flex-column" onChange={changeGalleryType}>
                        {galleryTypes.map((gallery, index) => (<ToggleButton variant='outline-secondary' id={`gt-${gallery.value}`} className={`p-4 text-start flex-column align-items-start w-100 mb-3 bg-white ${gallery.value == galleryType ? 'border-primary' : ''}`} value={gallery.value} style={{ minHeight: 130 }}>
                            <div className='d-flex align-items-center mb-2'>
                                <span className='f-center me-2 w-28px' style={{ color: gallery.color }}>
                                    <FontAwesomeIcon type={'light'} icon={gallery.icon} iconClass={'fs-5 me-0'} />
                                </span>
                                <span className='h5 mb-0'>{gallery.title}</span>
                            </div>
                            <p className='mb-0 fs-8 text-wrap text-muted fw-noraml'>{gallery.desc}</p>
                        </ToggleButton>))}
                    </ToggleButtonGroup>

                        :
                        <Dropdown className="w-100" onSelect={changeGalleryType}>
                            <Dropdown.Toggle variant='secondary' className='w-100 d-flex justify-content-between align-items-center  p-2'>
                                <div className='d-flex align-items-center'>
                                    <span className='f-center me-2 p-1 rounded-1 text-light w-28px' style={{ backgroundColor: galleryTypes[galleryType - 1].color }}>
                                        <FontAwesomeIcon type={'light'} icon={galleryTypes[galleryType - 1].icon} iconClass={'fs-5 me-0'} />
                                    </span>
                                    {galleryTypes[galleryType - 1].title}
                                </div>
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                {galleryTypes.map((gallery, index) => (
                                    <Dropdown.Item key={index} eventKey={gallery.value} className={`flex-column align-items-start w-100 mb-1 ${gallery.value == galleryType ? 'active' : ''}`}>
                                        <div className='d-flex align-items-center mb-2 text-body'>
                                            <span className='f-center me-2' style={{ color: gallery.color }}>
                                                <FontAwesomeIcon type={'light'} icon={gallery.icon} iconClass={'fs-4 me-0'} />
                                            </span>
                                            {gallery.title}
                                        </div>
                                        <p className='mb-0 fs-10 text-wrap text-gray-700'>{gallery.desc}</p>
                                    </Dropdown.Item>
                                ))}
                            </Dropdown.Menu>
                        </Dropdown>
                    }
                </div>
                {!isShopify ?
                    <>
                        <hr className='my-2' />
                        <div className='p-4'>
                            <h5>Select Platforms</h5>
                            <div className="mb-3">
                                <ToggleButtonGroup className='flex-column' type="radio" name="platform" value={platform} defaultValue={platform} onChange={changePlatform}>
                                    {platforms.map((s_platform, index) => (
                                        <ToggleButton key={index} id={`s_platform-${s_platform.value}`} value={s_platform.value} variant="secondary" className={`w-100 mb-3 py-3 me-0 ${s_platform.value == platform ? 'bg-body' : ''}`}>
                                            <Image className="h-26px" data-mode="light" src={baseUrl(`media/icons/cms-platform/${s_platform.name}.svg`)} height={26} width={280} fluid />
                                            <Image className="h-26px" data-mode="dark" src={baseUrl(`media/icons/cms-platform/${s_platform.name}-light.svg`)} height={26} width={280} fluid />
                                        </ToggleButton>
                                    ))}
                                    <RequestButtonHOC>
                                        <Button variant="secondary" size="lg" className={`w-100 p-3`}>
                                                <FontAwesomeIcon icon={'plus'}/>
                                                Request a Platform
                                        </Button>
                                    </RequestButtonHOC>
                                </ToggleButtonGroup>
                            </div>
                        </div>
                    </>
                    : null}
            </>
        )
    }
}
