import { connect } from "react-redux";
import React, { Component } from 'react'
import { OverlayTrigger, Form, Tooltip } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../../Assets/AssetsHelper";
import { CARD_STYLE_INHERIT, IS_CARD_STYLE_CHANGE } from '../../../../../../reducer/keys';

class InheritSwitch extends Component {
  render() {
    const { inheritStyle } = this.props
    return <div className="f-between w-100">
      <Form.Label className="" htmlFor="inherit_"><span className="me-2 fs-9">Use Your Website Settings (Recommended)</span>
        <OverlayTrigger placement='top' overlay={<Tooltip>{'Properties of your websites will be automatically applied on the widget once you embed.'}</Tooltip>} >
          <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-7'}/></span>
        </OverlayTrigger>
      </Form.Label>
      <Form.Check type="switch" id="inherit_" className="" defaultValue={inheritStyle} checked={inheritStyle == 1 ? true : false} onChange={(e) => { this.props.CARD_STYLE_INHERIT(Number(e.target.checked)) }} />
    </div>
  }
}

const stateToProps = (state) => {
  return {
    inheritStyle: state.appData?.wall?.ThemeRule?.inheritStyles
  }
}
const DispatchToProps = dispatch => {
  return {
    CARD_STYLE_INHERIT: (data) => { dispatch({ type: CARD_STYLE_INHERIT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
  }
}

export default connect(stateToProps, DispatchToProps)(InheritSwitch)