import React, { Component } from 'react'
import { Form, Row, Col } from 'react-bootstrap';
import RangeInput from '../../../CommonComponents/RangeInput';
import InheritSwitch from '../../Customization/Element/InheritSwitch';
import ColorPicker from '../../../CommonComponents/ColorPicker';
class Hotspot extends Component {

    render() {
        const { onChangeHotspotStatus, onChangeHotspotType, onChange_Hotspot_type_color, onChange_Hotspot_color, Hotspot, is_cta_setting_change } = this.props
        return (
            <>
                <div className="mt-4">
                    <Form.Check type="switch" id="hotspot_" className="f-between" label="Hotspot" defaultValue={Hotspot?.status} checked={Hotspot?.status} onChange={onChangeHotspotStatus} reverse />
                </div>
                <div className="mt-4">
                    <Form.Label className="mb-2" htmlFor="hotspot_icon">Hotspot icon</Form.Label>
                    <Row>
                        <Col>
                            <div className={`border border-2 p-1 rounded-1 cursor-pointer overlfow-hidden ${Hotspot?.type == 0 ? 'border-primary' : ''}`} onClick={() => onChangeHotspotType(0)}>
                                <div className='w-100 h-100 py-3 f-center flex-column rounded-1 icon-bg-gradient'>
                                    <div className='rounded-circle h-20px w-20px border-white mb-1' style={{ border: '5px solid' }}></div>
                                    <div className='fw-medium fs-9 text-white'>Dots</div>
                                </div>
                            </div>
                        </Col>
                        <Col>
                            <div className={`border border-2 p-1 rounded-1 cursor-pointer overlfow-hidden ${Hotspot?.type == 1 ? 'border-primary' : ''}`} onClick={() => onChangeHotspotType(1)}>
                                <div className='w-100 h-100 py-3 f-center flex-column rounded-1 icon-bg-gradient'>
                                    <div className='rounded-circle h-20px w-20px mb-1 fw-bold f-center fs-9 bg-white text-black-alt'>1</div>
                                    <div className='fw-medium fs-9 text-white'>Number</div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </div>
                <div className="mt-4">
                    <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`hotspot_bg`}>Background Color</Form.Label>
                    <ColorPicker id="hotspot_bg" defaultColor={Hotspot?.background} changeColor={onChange_Hotspot_color} colorChanged={is_cta_setting_change}/>
                </div>
                {Hotspot?.type ? <div className="mt-4">
                    <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`hotspot_text_color`}>Font Color</Form.Label>
                    <ColorPicker id="hotspot_text_color" defaultColor={Hotspot?.color} changeColor={onChange_Hotspot_type_color} colorChanged={is_cta_setting_change}/>
                </div> : null}
            </>
        )
    }
}

export default Hotspot;