import React, { PureComponent } from "react";
import './scss/Error.scss';
import Error201 from "./ErrorCode/201";
import Error202 from "./ErrorCode/202";
import Error203 from "./ErrorCode/203";
import Error204 from "./ErrorCode/204";
import Error205 from "./ErrorCode/205";
import Error206 from "./ErrorCode/206";
import Error207 from "./ErrorCode/207";
import Error208 from "./ErrorCode/208";
import Error209 from "./ErrorCode/209";
import Error210 from "./ErrorCode/210";
import Error211 from "./ErrorCode/211";
import Error212 from "./ErrorCode/212";
import Error404 from "./ErrorCode/404";
// import { checkPreview } from "../../../utils";
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import ErrorFound from "../../../../ErrorFound";


export default class ErrorPage extends PureComponent {
  render() {
    const { errorCode, wall, errorData } = this.props;
    // const errorCode= errorData.themeError.error_code; 

    const ErrorImage = { backgroundImage: `url(${baseUrl('media/images/error/nopost.png')})` }
    const LogoImage = { backgroundImage: `url(${baseUrl('media/images/logo/tagshop-logo.svg')})` }
    return (
      <div className="ts_error_page">
        <div className="ts_error_page_bg" style={ErrorImage}><div></div></div>
        <div className="ts_error_page_wrapper">
          <ErrorFound>
            {errorCode === 201 ? <Error201 />
              : errorCode === 202 ? <Error202 wall={wall} />
                : errorCode === 203 ? <Error203 wall={wall} />
                  : errorCode === 204 ? <Error204 wall={wall} />
                    : errorCode === 205 ? <Error205 wall={wall} />
                      : errorCode === 206 ? <Error206 wall={wall} />
                        : errorCode === 207 ? <Error207 wall={wall} />
                          : errorCode === 208 ? <Error208 wall={wall} />
                            : errorCode === 209 ? <Error209 wall={wall} />
                              : errorCode === 210 ? <Error210 wall={wall} />
                                : errorCode === 211 ? <Error211 wall={wall} />
                                  : errorCode === 212 ? <Error212 wall={wall} />
                                    : errorCode === 404 ? <Error404 wall={wall} error={errorData} />
                                      : errorCode === 400 ? <Error404 wall={wall} error={errorData} />
                                        : ''}
          </ErrorFound>
          <div className="ts_error_page_logo"> <span className="ts_error_page_logo_ico" style={LogoImage}></span> </div>
        </div>
      </div>
    );
  }
}
