import React, { Component } from 'react'
import Select from 'react-select';
import { Form, Row, Col } from 'react-bootstrap';
import ErrorFound from '../../../../../ErrorFound';
import RangeInput from '../../../CommonComponents/RangeInput';
import InheritSwitch from '../../Customization/Element/InheritSwitch';
import ColorPicker from '../../../CommonComponents/ColorPicker';
import SelectFont from '../../Components/SelectFont';
class CatalogName extends Component {

    render() {
        const { CatalogueTitle, onChangeCatelogFont, onChangeCatelogVeriant, onChangeFeature_font_size, onChangeFeature_text, inheritStyle, is_cta_setting_change } = this.props
        return (<>
            <ErrorFound><InheritSwitch /></ErrorFound>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`cta_title`}>Text</Form.Label>
                <Form.Control size='sm' placeholder="Featured Product" id="cta_title" defaultValue={CatalogueTitle?.text} onChange={onChangeFeature_text} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={onChangeCatelogFont} fontval={CatalogueTitle?.font} variantChange={onChangeCatelogVeriant} vrntVal={CatalogueTitle?.varient}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`cate_font_color`}>Font Color</Form.Label>
                <ColorPicker id="cate_font_color" defaultColor={CatalogueTitle?.color} changeColor={this.props.ChangeFeature_text_color} isDisabled={inheritStyle} colorChanged={is_cta_setting_change}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="radius_range">Font Size</Form.Label>
                <RangeInput id="radius_range" min={10} max={25} step={1} value={CatalogueTitle?.size} onChange={onChangeFeature_font_size} />
            </div>
        </>)
    }
}

export default CatalogName;