import React, { Component } from 'react'
import RangeInput from '../../../CommonComponents/RangeInput';
import InheritSwitch from '../../Customization/Element/InheritSwitch';
import { Form } from 'react-bootstrap';
import ColorPicker from '../../../CommonComponents/ColorPicker';
import ErrorFound from '../../../../../ErrorFound';
import SelectFont from '../../Components/SelectFont';
class Products extends Component {

    render() {
        const { onChangeProduct_font, onChangeProduct_verient, onChangeProduct_fontSize, onChangePrice_font, onChangePrice_verient, onChangePrice_fontSize, inheritStyle, onChangeProduct_color, onChangePrice_color, ProductTitle, is_cta_setting_change, Price } = this.props;

        return (<>
                <ErrorFound><InheritSwitch /></ErrorFound>
                <div className="mt-4">
                    <p className="h5">Title</p>
                    <SelectFont inheritStyle={inheritStyle} fontChange={onChangeProduct_font} fontval={ProductTitle?.font}
                    variantChange={onChangeProduct_verient} vrntVal={ProductTitle?.varient}/>
                </div>
                <div className="mt-4">
                    <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`pro_title_col`}>Color</Form.Label>
                    <ColorPicker id="pro_title_col" defaultColor={ProductTitle?.color} isDisabled={inheritStyle} changeColor={onChangeProduct_color} colorChanged = {is_cta_setting_change}/>
                </div>
                <div className="mt-4">
                    <Form.Label className="mb-2" htmlFor="radius_range">Font Size</Form.Label>
                    <RangeInput id="radius_range" min={10} max={25} step={1} value={ProductTitle?.size} onChange={onChangeProduct_fontSize} />
                </div>

                <hr className=""></hr>

                <div className="mt-4">
                    <p className="h5">Price</p>
                    <SelectFont inheritStyle={inheritStyle} fontChange={onChangePrice_font} fontval={Price?.font}
                    variantChange={onChangePrice_verient} vrntVal={Price?.varient}/>
                </div>
                <div className="mt-4">
                    <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`price_color`}>Color</Form.Label>
                    <ColorPicker id="price_color" defaultColor={Price?.color} isDisabled={inheritStyle} changeColor={onChangePrice_color} colorChanged = {is_cta_setting_change}/>
                </div>
                <div className="mt-4">
                    <Form.Label className="mb-2" htmlFor="radius_range">Font Size</Form.Label>
                    <RangeInput id="radius_range" min={10} max={25} step={1} value={Price?.size} onChange={onChangePrice_fontSize} isDisabled={inheritStyle} />
                </div>
            </>)
    }
}

export default Products;