import React, { Component } from 'react'
import Select from 'react-select';
import ErrorFound from '../../../../../ErrorFound';
import { isShopify } from '../../../../../../utils';
import { Form, Row, Col } from 'react-bootstrap';
import RangeInput from '../../../CommonComponents/RangeInput';
import InheritSwitch from '../../Customization/Element/InheritSwitch';
import ColorPicker from '../../../CommonComponents/ColorPicker';
import SelectFont from '../../Components/SelectFont';

const options = [
    { value: 1, label: 'Open New Tab' },
    { value: 2, label: 'Continue Same Tab' },
]
class ProductButton extends Component {

    styles = {
        option: (provided, state) => ({ ...provided, fontFamily: state.data.font }),
        singleValue: (provided, state) => ({ ...provided, fontFamily: state.data.font })
    };

    render() {
        const {buy_now_text, buy_now_color, buy_now_background , button_css_font, button_font_varient, buy_font_size,  onChangeButton_text, onChangeButton_font, onChangeButton_verient, onChangeButton_fontSize, allFont, onChange_tab_target, tab_target, onChange_pdp_target, pdp_target,  inheritStyle, onChangeButton_color, onChangeButton_background, ProdButton, is_cta_setting_change} = this.props

        // var fontSelected = allFont.fonts.filter((itm) => itm.label == button_css_font)
        // const f = allFont.allFonts.filter((font) => font.family === button_css_font)[0]
        // var veriantData = f && f.variants? JSON.parse(f.variants).map((f, i) => { return ({ 'label': f, 'value': i, 'className': 'redmmt', 'font': button_css_font }) }) : ""
        // var variantId = veriantData && veriantData.filter((itm) => itm.label == button_font_varient)
        // var variantId2 = veriantData && veriantData.filter((itm) => itm.value == 0)
        // var variantSelected =variantId.length > 0 ? variantId : variantId2

        return (
            <>
            <ErrorFound><InheritSwitch /></ErrorFound>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`btn_text`}>Text</Form.Label>
                <Form.Control size='sm' placeholder="Featured Product" id="btn_text" defaultValue={ProdButton?.text} onChange={onChangeButton_text} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={onChangeButton_font} fontval={ProdButton?.font} variantChange={onChangeButton_verient} vrntVal={ProdButton?.varient}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`shop_btn_color`}>Font Color</Form.Label>
                <ColorPicker id="shop_btn_color" defaultColor={ProdButton?.color} changeColor={onChangeButton_color} colorChanged={is_cta_setting_change}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`shop_btn_bg`}>Background Color</Form.Label>
                <ColorPicker id="shop_btn_bg" defaultColor={ProdButton?.background} changeColor={onChangeButton_background} colorChanged={is_cta_setting_change}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="shop_btn_size">Font Size</Form.Label>
                <RangeInput id="shop_btn_size" min={10} max={25} step={1} value={ProdButton?.size} onChange={onChangeButton_fontSize}/>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="on_btn_click">On Click</Form.Label>
                <Select className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={options} onChange={onChange_tab_target} defaultValue={{value: 1, label: 'Open New Tab'}} value={options[options.findIndex(i => parseInt(i.value) == parseInt(ProdButton?.newTab))]}/> 
            </div>
            {isShopify() ?
            <div className="mt-4">
                <Form.Check type="switch" id="redirect_pdp" className="f-between" label="Redirect to PDP(Product Page)" defaultValue={ProdButton?.pdp_target} checked={ProdButton?.pdp_target} onChange={onChange_pdp_target} reverse/>
            </div> : null}
        </>
        )
    }
}

export default ProductButton;