import React, { Component } from 'react'
import { connect } from 'react-redux'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { Button, Row, Col, Image } from 'react-bootstrap'
import { APPLY_THEME_ACTION, CHANGE_THEME_ACTION } from '../../../../../actions/themeAction'
import { checkPermission } from '../../../../../actions/collaboratorActions'
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC'

class ThemeList extends Component {
    state = { selectedtheme: false, themeId: this.props.appData?.wall?.Personalization.commerceTheme }
    onClose = () => this.setState({ selectedtheme: false, themeId: null })

    componentDidMount() {
        this.setState({ themeId: this.props.appData?.wall?.Personalization.commerceTheme })
    }
    changeThemes = (themeID) => {
        this.setState({ selectedtheme: true, themeId: themeID })
        this.props.CHANGE_THEMES(themeID);
    }

    onApplyTheme = () => {
    }


    componentDidUpdate(prevProps) {
        if (this.props.appData.save_and_publish === true && prevProps.appData.save_and_publish !== true) {
            if (this.props.appData.is_themes_change) {
                this.props.APPLY_THEME(this.state.themeId, this.onApplyTheme);
            }

        }
        if (this.props.appData?.wall?.Personalization.commerceTheme !== prevProps.appData?.wall?.Personalization.commerceTheme) {
            this.setState({ themeId: this.props.appData?.wall?.Personalization.commerceTheme })
        }
    }


    render() {
        const { themeList, appData } = this.props
        const { themeId } = this.state
        return (<div className='px-4 pb-3 mb-4'>
            <Row className="select-group flex-wrap row gx-8 border-top">
                {themeList.map((Item, Index) => (
                    <Col key={Index} className="col-4 p-0 select-grid">
                        <Button data-theme={Item.themeId} variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100 ${Item.themeId == themeId ? 'active' : ''}`} onClick={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId) : {}}>
                            <div className="w-50px h-50px mb-2">
                                <Image className="invert" src={baseUrl(`media/icons/theme/${Item.themeId}.svg`)} height={46} width={150} alt={Item.name} fluid/>
                            </div>
                            <p className="mb-0 text-center">{Item.name}</p>
                        </Button>
                    </Col>
                ))}
                <Col className="col-4 p-0 select-grid">
                    <RequestButtonHOC>
                        <Button variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100`}>
                            <div className="w-50px h-30px mb-2 f-center">
                                <FontAwesomeIcon type={'light'} icon={'plus-circle'} iconClass={'fs-1 me-0 text-gray-400'}/>
                            </div>
                            <p className="mb-0 text-center">Request New Themes</p>
                        </Button>
                    </RequestButtonHOC>
                </Col>
            </Row>
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        appData: state.appData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        CHANGE_THEMES: (id) => dispatch(CHANGE_THEME_ACTION(id)),
        APPLY_THEME: (id, callback) => dispatch(APPLY_THEME_ACTION(id, callback))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeList);