import HttpClient from './http-client'
import store from '../store'
import { GET_ALL_THEME, APPLY_THEME, MODERATION_API_GET_DETAILS, CHANGE_THEME } from './api'
import { DISPATCH_GET_ALL_THEME, DISPATCH_APPLY_THEME, DISPATCH_GET_WALL_DETAILS, APPLYED_NEW_THEME, LOADING_WEBSITE_REDUCER, CHANGE_THEME_ID, GET_APP_DATA, IS_THEMES_CHANGE, LIVE_PREVIEW_LOADER } from '../reducer/keys'
import { CHANGE_WEBSITE_WALL, GET_ALL_WEBSITE_LIST } from './websiteAction'

export const GET_THEME_LIST = () => {
    return (dispatch) => {
        new HttpClient().get(GET_ALL_THEME).then((responce) => {
            const { responseData } = responce.data
            dispatch({
                type: DISPATCH_GET_ALL_THEME,
                payload: responseData,
            });
        }).catch((error) => {
            console.log("ERROR", error)
        })
    }
}

export const GET_WALL_DETAILS = () => {

    var wall_id = localStorage.getItem('wallId') ? localStorage.getItem('wallId') : '';

    return (dispatch) => {
        new HttpClient().post(`${MODERATION_API_GET_DETAILS}`, {
            ownerId: localStorage.getItem('ownerId'),
            wallId: wall_id
        }).then((detailReponse) => {
            const { wall } = detailReponse.data;
        }).catch((error) => {
            console.log("ERROR", error)
        })
    }
}




export const APPLY_THEME_ACTION = (id, callBack) => {
    var { wall } = store.getState().appData;
    const personalizationId = wall?.Personalization?.id;
    const frpmdata = {
        "theme": id,
        "themeTypePre": wall?.Personalization?.themeType,
        "textDecorateStatus": wall?.Personalization?.textDecorateStatus,
        "postFeaturedStatus": wall?.Personalization?.postFeaturedStatus,
        "filterEnableStatus": wall?.Personalization?.filterEnableStatus,
        "textDecorate": wall?.Personalization?.textDecorate,
        "postFeatured": wall?.Personalization?.postFeatured,
        "filterStatus": wall?.Personalization?.filterStatus,
        "themeType": wall?.Personalization?.themeType,
        "trimcontent": wall?.Personalization?.trimcontent,
        "trimContentStatus": wall?.Personalization?.trimContentStatus,
        "popupSlideShow": wall?.Personalization?.postFeaturedStatus,
        "popupSlideShowStatus": wall?.Personalization?.postFeaturedStatus,
        "padding": wall?.Personalization?.padding
    }
    return (dispatch) => {
        new HttpClient().post(`${APPLY_THEME}/${localStorage.getItem('wallId')}/${personalizationId}/${localStorage.getItem('website_id')}`, frpmdata).then((response) => {
            const { responseData, responseCode } = response.data;
            if (responseCode == 200) {
                dispatch({ type: GET_APP_DATA, payload: store.getState().appData, heightEvent: null });
                setTimeout(() => {
                    dispatch({ type: CHANGE_THEME_ID, payload: id });
                    dispatch(GET_ALL_WEBSITE_LIST());
                }, 500);
            }
            callBack();
        }).catch((error) => {
            console.log("ERROR", error)
        });

    }
}



export const CHANGE_THEME_ACTION = (id) => {

    var { wall } = store.getState().appData;
    const personalizationId = wall?.Personalization?.id;
    const frpmdata = {
        "theme": id,
        "themeTypePre": wall?.Personalization?.themeType,
        "textDecorateStatus": wall?.Personalization?.textDecorateStatus,
        "postFeaturedStatus": wall?.Personalization?.postFeaturedStatus,
        "filterEnableStatus": wall?.Personalization?.filterEnableStatus,
        "textDecorate": wall?.Personalization?.textDecorate,
        "postFeatured": wall?.Personalization?.postFeatured,
        "filterStatus": wall?.Personalization?.filterStatus,
        "themeType": wall?.Personalization?.themeType,
        "trimcontent": wall?.Personalization?.trimcontent,
        "trimContentStatus": wall?.Personalization?.trimContentStatus,
        "popupSlideShow": wall?.Personalization?.postFeaturedStatus,
        "popupSlideShowStatus": wall?.Personalization?.postFeaturedStatus,
    }
    return (dispatch) => {
        dispatch({ type: LIVE_PREVIEW_LOADER, payload: true });
        const wallId = localStorage.getItem('active_wall') ? localStorage.getItem('active_wall') : localStorage.getItem('wallId')
        new HttpClient().post(`${CHANGE_THEME}/${wallId}/${personalizationId}/${localStorage.getItem('website_id')}`, frpmdata).then((response) => {
            const { responseData, responseCode } = response.data;

            if (responseCode == 200) {
                dispatch(CHANGE_WEBSITE_WALL(localStorage.getItem('active_wall'), responseData.wall?.ThemeRule.numberOfPosts ? responseData.wall?.ThemeRule.numberOfPosts : 20, responseData.wall?.ThemeRule.themeId))
                dispatch({ type: GET_APP_DATA, payload: responseData, heightEvent: null });
                setTimeout(() => {
                    dispatch({ type: CHANGE_THEME_ID, payload: id });
                    dispatch({ type: IS_THEMES_CHANGE, payload: true });
                }, 200);
                setTimeout(() => {
                    dispatch({ type: LIVE_PREVIEW_LOADER, payload: false });
                }, 2000);
            }
        }).catch((error) => {
            dispatch({ type: LIVE_PREVIEW_LOADER, payload: false });
            console.log("ERROR", error)
        });

    }
}