import Swal from "sweetalert2"
import { GET_BIGCOMMERCE_DATA, GET_SHOPIFY_DATA } from "../reducer/keys"
import { BIGCOMMERCE_EMBED, BIGCOMMERCE_EMBED_DELETE, BIGCOMMERCE_GET_PUBLISH, SHOPIFY_DELETE_PUBLISH, SHOPIFY_GET_PUBLISH, SHOPIFY_PUBLISH } from "./api"
import HttpClient from "./http-client"

export const GET_BIGCOMMERCE_PUBLISH_DATA = () => {
    const obj = new FormData()
    obj.append('wallId', localStorage.getItem('wallId'))
    return (dispatch) => {
        new HttpClient().get(`${BIGCOMMERCE_GET_PUBLISH}/${localStorage.getItem('wallId')}`).then(res => {
            const { status, data } = res.data;
            dispatch({
                type: GET_BIGCOMMERCE_DATA,
                payload: data,
            });
        }).catch((error) => {
            console.log("ERROR", error)
        });
    }
}

export const DELETE_BIGCOMMERCE_PUBLISHED = (id) => {
    return (dispatch) => {
        new HttpClient().post(BIGCOMMERCE_EMBED_DELETE, { id })
            .then((response) => {
                const { status, data } = response.data;
                if (status) {
                    dispatch(GET_BIGCOMMERCE_PUBLISH_DATA());
                    Swal.fire({
                        title: "Deleted!",
                        icon: "success",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                }
            }).catch((error) => {
                console.log("ERROR", error)
            });
    }
}

export const PUBLISH_BIGCOMMERCE_GALLERY = (obj) => {
    return (dispatch) => {
        new HttpClient().post(BIGCOMMERCE_EMBED, obj)
            .then((response) => {
                const { status } = response.data;
                if (status) {
                    dispatch(GET_BIGCOMMERCE_PUBLISH_DATA());
                    Swal.fire({
                        title: "Publish successfully",
                        icon: "success",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                }
                else {
                    Swal.fire({
                        title: response.data.message,
                        icon: "error",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                }

            }).catch((error) => {
                console.log("ERROR", error)

            });
    }
}