import { useEffect, useState } from "react";
import { Dashboard } from "@uppy/react";
import { Button, Form, Image, Alert } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { addBackground } from "../../../../../actions/customization";
import { CHANGE_BACKGROUND_COLOR, CHANGE_BACKGROUND_STATUS, CHANGE_BACKGROUND_IMAGE, IS_BACKGROUN_CHANGE } from "../../../../../reducer/keys";
import ColorPicker from "../../CommonComponents/ColorPicker";
import UppyUpload from "../../../../../utils/uppyUpload";
import { ownerId } from "../../../../../utils/localstorage";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";

const uppyUploader = new UppyUpload()
const BackgroundStyle = ({ backgroundDetails, openBanner, PersonalizationID, bannerDetail, save_and_publish, is_backgroun_change }) => {
  const dispatch = useDispatch()
  const [selectedFile, setSelectedFile] = useState(backgroundDetails?.Background?.image ? backgroundDetails?.Background?.image : "");

  let transparentBg = backgroundDetails?.Background?.transparent
  let bgImage = backgroundDetails?.Background?.image ? backgroundDetails?.Background?.image : ""
  let bgColor = backgroundDetails?.Background?.color ? backgroundDetails.Background?.color : '#ffffff'

  useEffect(() => {
    uppyUploader.uppy.on("transloadit:result", (stepName, result) => {
      setSelectedFile(`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`);
      dispatch({ type: CHANGE_BACKGROUND_IMAGE, payload: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}` })
      setTimeout(() => { uppyUploader.uppy.cancelAll(); }, 300);
    });

  }, []);

  useEffect(() => {
    if (save_and_publish == true && is_backgroun_change == true) onApplyBackground();
  }, [save_and_publish]);


  const onApplyBackground = () => {
    const backgroundObj = { id: PersonalizationID, transparent: transparentBg ? 1 : 0, backgroundColor: bgColor, backgroundRemove: bgImage.length ? 0 : 1, themeId: backgroundDetails.ThemeRule.themeId, ownerId: ownerId, picture: bgImage ? `${bgImage}` : '', isPictureChanged: 1, ownerId:localStorage.getItem('ownerId')};
    addBackground(backgroundObj).then((res) => { })
  }


  return <>
    <Form.Label className="mb-2" htmlFor="background">Transparent Background</Form.Label>
    <Form.Check type="switch" id="background" className="w-100" defaultValue={transparentBg} checked={transparentBg} onChange={(e) => {dispatch({ type: CHANGE_BACKGROUND_STATUS, payload: Number(e.target.checked) }); dispatch({ type: IS_BACKGROUN_CHANGE, payload: true }); }} />
    {!transparentBg ?
      <>
        <div className="mt-4">
          <Form.Label className="mb-2" htmlFor="bgColor">Color</Form.Label>
          <ColorPicker id="bgColor" defaultColor={bgColor} changeColor={(clr) => { dispatch({ type: CHANGE_BACKGROUND_COLOR, payload: clr }); dispatch({ type: IS_BACKGROUN_CHANGE, payload: true }) }} colorChanged={is_backgroun_change}/>
        </div>
        <div className="mt-4">
          <Form.Label className="mb-2">Background Image</Form.Label>
          {bgImage ? <div className="position-relative">
            <Image id="bg_image" className="object-fit-contain" src={bgImage} height={300} width={300} alt="" style={{ maxHeight: 250 }} fulid />
            <div className="position-absolute" style={{ top: -10, right: -10 }}>
              <Button variant="icon" size="sm" className="rounded-circle btn-active-primary" onClick={() => { dispatch({ type: CHANGE_BACKGROUND_IMAGE, payload: '' }); dispatch({ type: IS_BACKGROUN_CHANGE, payload: true }); }}><FontAwesomeIcon icon={'xmark'} /></Button>
            </div>
          </div>
          :
          <div className="uppy_scale">
          <Dashboard proudlyDisplayPoweredByUppy={false} hideUploadButton={true} showProgressDetails={true} disableThumbnailGenerator={false} height={360} showRemoveButtonAfterComplete={false} uppy={uppyUploader.uppy} plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]} />
          </div>
          }
        </div>
      </>
      : null}

    {!transparentBg ? <Alert className="mt-2 mb-0 rounded-0 fs-9 p-3" variant="info">
      Recommended Size: 1920px x 1080px Image with Aspect Ratio and Resolution of Intended Display.
    </Alert> : null}
  </>
};

export default BackgroundStyle;
