import React, { PureComponent } from "react";
import moment from 'moment';
export default class Error208 extends PureComponent {
  render() {
    const { wall,error } = this.props;
    return (
      <div className="ts_error_content">
        <div className="ts_error_title">No Posts!</div>
        <div className="ts_error_des">It seems available posts are older than {error.older_days !== 8 ? `${error.older_days} Days` : moment(new Date(error.older_post * 1000)).format('LL')}
          {'. '} All new posts will appear here.</div>

      </div>
    );
  }
}
