import { useEffect } from "react";
import { onSiteUpload } from "../../../../../actions/customization";
import { useDispatch, useSelector } from "react-redux";
import ColorPicker from "../../CommonComponents/ColorPicker";
import { BTN_FONTSIZE, ONSITE_BTN_COLOR, ONSITE_BTN_TXT_COLOR, ONSITE_CSS_FONT_BTN, ONSITE_FONT_VARIENT_BTN, ONSITE_QR_CODE_BTN_TEXT, ONSITE_QR_CODE_STATUS } from "../../../../../reducer/keys";
import { Form } from "react-bootstrap";
import SelectFont from "../Components/SelectFont";
import RangeInput from "../../CommonComponents/RangeInput";
import ToggleButton from "./Element/ToggleButton";

const UGCUploads = ({ ugcDetails, PersonalizationID, save_and_publish, is_magic_upload_change }) => {
  
  const dispatch = useDispatch()
  const fontsList = useSelector((state) => state.allFont?.allFonts)
  const Personalization = useSelector(state => state.addFeedReducer?.details?.wall?.Personalization)
  const ugc_settings = Personalization?.onsite_settings ? JSON.parse(Personalization.onsite_settings).taggshop : null
  const styles = {
    option: (provided, state) => ({ ...provided, fontFamily: state.data.font }),
    singleValue: (provided, state) => ({ ...provided, fontFamily: state.data.font })
  };

  useEffect(() => {
    if (save_and_publish == true && is_magic_upload_change == true) applySettings();
  }, [save_and_publish]);


  const applySettings = () => {
    var onsiteObj = {
      wallId: localStorage.getItem('active_wall'),
      websiteId: localStorage.getItem('website_id'),
      ownerId: localStorage.getItem('ownerId'),
      id: PersonalizationID,
      onsite_qr_code_btn_text: ugcDetails.onsite_qr_code_btn_text ? ugcDetails.onsite_qr_code_btn_text : '',
      onsite_btn_color: ugcDetails.onsite_btn_color ? ugcDetails.onsite_btn_color : '#000000',
      onsite_btn_txt_color: ugcDetails.onsite_btn_txt_color ? ugcDetails.onsite_btn_txt_color : '#ffffff',
      onsite_link_font_btn:  ugcDetails.onsite_css_font_btn ? ugcDetails.onsite_css_font_btn.replace(/\s+/g, '+') : 'Lato',
      onsite_css_font_btn: ugcDetails.onsite_css_font_btn ? ugcDetails.onsite_css_font_btn.replace(/\s+/g, '+') : 'Lato',
      onsite_font_varient_btn: ugcDetails.onsite_font_varient_btn ? ugcDetails.onsite_font_varient_btn : '',
      btn_fontSize: ugcDetails.btn_fontSize ? ugcDetails.btn_fontSize : 16,
      onsite_qr_code_status: ugcDetails.onsite_qr_code_status ? 1 : 0,
    }
    if (ugc_settings) {
      onsiteObj = { ...ugc_settings, ...onsiteObj }
    }
    onSiteUpload(onsiteObj)
  }

  const onFontChange = (event) => {
    const selectedFont = fontsList.find(font => font.family == event.label.key)
    let selectVariant = JSON.parse(selectedFont.variants).find(item => item == ugcDetails.onsite_css_font_btn)
    dispatch({ type: ONSITE_CSS_FONT_BTN, payload: selectedFont.family })
    changeVariant('change',selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
  }

  const changeVariant = (e,val) => {
    dispatch({ type: ONSITE_FONT_VARIENT_BTN, payload: e == 'change' ? val : e.label.key });
  }

  return (
    
    <div className="">
      <div className='form-group mb-2'>
        <h6 className="titleMagicUpload mt-3 mb-4">Button Content</h6>
      </div>
      <div className="mt-4">
        <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`ugc_title`}>Button Text</Form.Label>
        <Form.Control size='sm' placeholder="Snap Up Text" id="ugc_title" defaultValue={ugcDetails.onsite_qr_code_btn_text} onChange={(e) => {dispatch({ type: ONSITE_QR_CODE_BTN_TEXT, payload: e.target.value }) }} />
      </div>

      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="ugc_bg_color">Background Color</Form.Label>
        <ColorPicker id="ugc_bg_color" isDisabled={""} defaultColor={ugcDetails.onsite_btn_color ? ugcDetails.onsite_btn_color : '#000000'} changeColor={(clr) => { dispatch({ type: ONSITE_BTN_COLOR, payload: clr }) }} colorChanged={is_magic_upload_change} />
      </div>

      <div className="mt-4">
        <SelectFont fontChange={onFontChange} variantChange={changeVariant} fontval={ugcDetails.onsite_css_font_btn} vrntVal= {ugcDetails.onsite_font_varient_btn}/>
      </div>

      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="ugc_fnt_color">Font Color</Form.Label>
        <ColorPicker id="ugc_fnt_color" isDisabled={""} defaultColor={ugcDetails.onsite_btn_txt_color ? ugcDetails.onsite_btn_txt_color : '#ffffff'} changeColor={(clr) => { dispatch({ type: ONSITE_BTN_TXT_COLOR, payload: clr }) }} colorChanged={is_magic_upload_change} />
      </div>

      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="radius_range">Font Size</Form.Label>
        <RangeInput id="radius_range" min={10} max={30} step={1} value={ugcDetails.btn_fontSize} onChange={(e) => { dispatch({ type: BTN_FONTSIZE, payload: e }) }} />
      </div>

      <div className="mt-4">
        <ToggleButton toggleName="QR(Hide/Show)" toggleValue={String(ugcDetails.onsite_qr_code_status) ? parseInt(ugcDetails.onsite_qr_code_status) : 1} callbackFn={()=>{}} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: ONSITE_QR_CODE_STATUS, payload: Number(status) }) } />
      </div>
    </div>
  );
};

export default UGCUploads;
