import React, { Component } from 'react'
import { Button, InputGroup } from 'react-bootstrap';
import { autoCompleteTag, tagsEmbedSave, ADD_SELECTED_TAGS, REMOVE_SELECTED_TAGS } from '../../../../../../actions/moderationActions'
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { debounce, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../../utils';
import { WEBSITE_EMBED_URL } from '../../../../../../actions/api';
import Select from 'react-select';

export const NumberBullet = ({count}) => <span className='d-inline-block text-center h-20px w-20px rounded-circle me-2 bg-primary text-gray-100 fs-9 flex-shrink-0' style={{ lineHeight: '20px' }}>{count}</span>
class TagBase extends Component {
    state = { searchData: [], isLoading: false, searchPost: null, isPreview: false, dataTag: false, copied: false }

    copyText = (e) => {
        var copyText = document.getElementById('wallembedareaLiteCode03').value
        navigator.clipboard.writeText(copyText);
        this.setState({ copied: true })
        setTimeout(() => { this.setState({ copied: false }) }, 1000)

    }

    onLoad = debounce((e) => {
        var text = e;
        if (text.length > 0) {
            this.setState({ isLoading: true })
            autoCompleteTag(text).then((response) => {
                const { responseData, responseStatus } = response.data;
                const { tags } = responseData
                let tagList = []
                if (responseStatus){
                    if(tags.length > 0) {
                        tagList = tags.map((item,index) => {return {label: item, value:index}})
                    }
                    this.setState({ searchData: tagList, isLoading: false })
                } 
                else this.setState({ searchData: [], isLoading: false })

            })
        }
        else {
            this.setState({ searchData: [], isLoading: false })
        }
    },500)

    componentDidMount() { if (this.props.allTags.length > 0) this.setState({ dataTag: true }); }

    onClickToAddTags = (tag) => {
        const { allTags } = this.props;
        if (!allTags.includes(tag.label)) this.props.ADD_SELECTED_TAGS(tag.label);
    };
    removeTags = (tag) => { this.props.REMOVE_SELECTED_TAGS(tag); setTimeout(() => { this.onTagsEmbedSave(3) }, 500) }

    onTagsEmbedSave = (id) => {
        const { allTags } = this.props;
        if (id == 1) {
            this.setState({ isPreview: false })
            if (allTags.length > 0) this.setState({ dataTag: true },()=>this.props.updateData(true));
            TOAST_SUCCESS('Embed code generated successfully.')
        }
        if (id == 2) {
            if (allTags.length > 0) this.setState({ isPreview: true, dataTag: true },()=>this.props.updateData(true));
            else TOAST_ERROR("Please select tag.")
        }
        if (id == 3) {
            if (!(allTags.length > 0)) this.setState({ isPreview: false, dataTag: false },()=>this.props.updateData(false));
        }
        tagsEmbedSave(allTags).then((response) => {
            const { tags, status } = response.data;
            if (status) this.setState({ searchData: tags, isLoading: false })
            else this.setState({ searchData: [], isLoading: true })
        })
    }

    render() {
        const { searchData, isPreview, dataTag, isLoading } = this.state
        const { allTags, count } = this.props;
        const selectTags = allTags?.length > 0 ? allTags.map((item,index) => {return{label:item,value:index}}) : []
        return (<div style={{maxWidth:991}}>
                    <div className='px-lg-3'>
                        <p className="h6 mb-4 d-flex"><NumberBullet count={count} />Select your tags</p>
                        <div className='mb-3'>
                            <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" options={searchData} onInputChange={(e,{action}) => this.onLoad(e,action)} onChange={(e) => this.onClickToAddTags(e)} isLoading={isLoading} placeholder="Search for options..." noOptionsMessage={() => isLoading ? 'Loading...' : 'No options found'} value={''}/>
                        </div>
                        <div className="mb-1">
                            <div className="tags-group d-flex align-items-center flex-wrap">
                                {allTags.length>0 ? allTags.map((item,index)=>{
                                    return <div data-id="tag" key={index} className="badge bg-secondary px-3 py-2 border me-2 mb-2">
                                    <span className="text-break" style={{ whiteSpace: 'normal' }}>{item}</span>
                                    <Button variant='link' size='sm' className="p-0 ms-1 text-gray-800" onClick={() => this.removeTags(item)}>
                                        <FontAwesomeIcon icon={'xmark'} iconClass={'me-0'}/>
                                    </Button>
                                </div>
                                }):null}
                            </div>
                        </div>
                    </div>
                    <div className='px-lg-3 mb-5'>
                        <div className='d-flex align-items-center justify-content-start mb-5'>
                            <Button id="generate_code_" className="me-2" disabled={allTags.length > 0 ? false : true} onClick={() => this.onTagsEmbedSave(1)}>
                            <FontAwesomeIcon icon={'sparkles'} />Generate Code</Button>
                            <Button id="preview_btn_" variant='secondary' disabled={allTags.length > 0 ? false : true}  onClick={() => this.onTagsEmbedSave(2)}>Preview</Button>
                        </div>

                        {isPreview ? <div className='tag_iframe_preview mb-5 border p-1 rounded-1'>
                            <iframe id="tag_preview" src={`${WEBSITE_EMBED_URL}${localStorage.getItem('website_id')}/editor?tags=true&keywords=${dataTag ? allTags.length > 0 ? allTags.join(',') : '' :''}&website=true`}></iframe>
                        </div> : ''}
                    </div>

            </div>
        )
    }
}

const mapStateToProps = state => {
    const { allTags } = state
    return {
        allTags: allTags?.selectedTags
    }
}
const mapDispatchToProps = dispatch => {
    return {
        ADD_SELECTED_TAGS: (tag) => dispatch(ADD_SELECTED_TAGS(tag)),
        REMOVE_SELECTED_TAGS: (tag) => dispatch(REMOVE_SELECTED_TAGS(tag)),
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(TagBase)