import React, { useState, useEffect, useRef } from 'react';
import { OverlayTrigger, Tooltip, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { useSelector, useDispatch } from 'react-redux';
import { GOOGLE_TRACKING, GOOGLE_TRACKING_CODE, IS_UTM_TRACKER_CHANGE } from '../../../../../reducer/keys';
import { UPDATE_UTM_TRACKER } from '../../../../../actions/websiteAction';

const GoogleAnalytics = ({ getData, save_and_publish, is_utm_tracker_change  }) => {


    const {Wall} = useSelector((state) => state.appData?.wall);
    const dispatch = useDispatch();  
    const [trackingIdError, setTrackingIdError] = useState(false) 


    const applyUTM_setting = () => {
      if (Wall?.google_tacking_code.trim().length == 0 && Wall?.google_tacking_status == 1) { setTrackingIdError(true); return false }
      let code = Wall?.google_tacking_code
      let status = Wall?.google_tacking_status
      let utmStatus = Wall?.utm_status
      let utmSource = Wall?.utm_source
      let utmMedium = Wall?.utm_medium
      let utmCampaign = Wall?.utm_campaign
      dispatch(UPDATE_UTM_TRACKER({code, status, utmStatus, utmSource, utmCampaign, utmMedium}))
    }

    useEffect(() => {
   if(save_and_publish==true && is_utm_tracker_change==true) applyUTM_setting()
    }, [save_and_publish]);

    return <div className="mt-4">
            <div className="f-between">
              <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`google_anly`}>
                  <span className='me-2'>Google Analytics</span>
                  <OverlayTrigger placement='top' overlay={<Tooltip>Allow Google Analytics tracking for this website.</Tooltip>} >
                      <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                  </OverlayTrigger>
              </Form.Label>
              <Form.Check type="switch" id={`google_anly`} checked={Wall?.google_tacking_status} onChange={(e) => {setTrackingIdError(false); dispatch({type:GOOGLE_TRACKING, payload:e.target.checked})}} />
              </div>
              <div className="mt-4">
                <Form.Control placeholder="e.g. UA-123456-1 A" className={`${trackingIdError ? 'is-invalid' :''}`}  value={Wall?.google_tacking_code} maxLength="100" type="text" onChange={(e) => {setTrackingIdError(false); dispatch({type:GOOGLE_TRACKING_CODE, payload:e.target.value})}} disabled={!Wall?.google_tacking_status ? true : false}/>
                {trackingIdError ? <span className="invalid-feedback"> Please Fill Tracking ID </span>:''}
              </div>
            </div>
};

export default GoogleAnalytics;
