import React, { PureComponent } from "react";
import { Link } from "react-router-dom";

export default class Error202 extends PureComponent {
  render() {
    const { wall } = this.props;
    return (
      <div className="ts_error_content">
        <div className="ts_error_title">No Feeds!</div>
        <div className="ts_error_des">It seems you have not added any Feed. Please Add Social Networks !</div>
        <div className="w-100 f-center">
          <Link style={{ cursor: 'pointer' }} to={`/content/addfeed/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') :'' }`} className="btn btn-primary text-nowrap btn-lg">Add feed</Link> 
          </div>
      </div>
    );
  }
}
