import React, { Component } from 'react'
import { connect } from "react-redux";
import { Accordion } from 'react-bootstrap';
import CatalogName from './Element/CatalogName';
import Products from './Element/Products';
import ProductButton from './Element/ProductButton';
import Hotspot from './Element/Hotspot';
import { UPDATE_CTA_ACTION } from '../../../../../actions/moderationActions';
import { FEATURE_TEXT, FEATURE_TEXT_COLOR, FEATURE_CSS_FONT, FEATURE_FONT_VARIENT, FEATURE_FONT_SIZE, BUY_NOW_TEXT, BUTTON_CSS_FONT, BUTTON_FONT_VARIENT, BUY_NOW_COLOR, BUY_NOW_BACKGROUND, BUY_FONT_SIZE, PRODUCT_TITLE_COLOR, PRODUCT_TITLE_FONT_SIZE, TITLE_CSS_FONT, TITLE_FONT_VARIENT, ORIGINAL_PRICE_COLOR, PRICE_CSS_FONT, PRICE_FONT_VARIENT, ORIGINAL_PRICE_FONT_SIZE, DISCOUNT_PRICE_COLOR, DISCOUNT_CSS_FONT, DISCOUNT_FONT_VARIENT, DISCOUNT_PRICE_FONT_SIZE, HOTSPOT, HOTSPOT_COLOR, HOTSPOT_TYPE, HOTSPOT_TYPE_COLOR, STATUS, TAB_TARGET, PDP_TARGET } from '../../../../../reducer/keys';
class CTAButtonStyle extends Component {

    onChangeCatelogFont = (event) => {
        const { allFont, productSettings } = this.props
        const selectedFont = allFont.find(font => font.family == event.label.key)
        let selectVariant = JSON.parse(selectedFont.variants).find(item => item == productSettings?.CatalogueTitle.varient)
        this.props.FEATURE_CSS_FONT(selectedFont.family)
        this.onChangeCatelogVeriant('change', selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
    }

    onChangeCatelogVeriant = (e, val) => {
        this.props.FEATURE_FONT_VARIENT(e == 'change' ? val : e.label.key)
    }

    onChangeFeature_text = (e) => { this.props.FEATURE_TEXT(e.target.value) };

    onChangeFeature_font_size = (e) => { this.props.FEATURE_FONT_SIZE(parseInt(e)) }

    onChangeStatus = () => { this.setState({ status: this.state.status == 1 ? 0 : 1 }); this.props.STATUS(this.state.status == 1 ? 0 : 1) }

    onChangeButton_text = (e) => { this.props.BUY_NOW_TEXT(e.target.value) };

    onChangeButton_font = (e) => {
        const { allFont, productSettings } = this.props
        const selectedFont = allFont.find(font => font.family == e.label.key)
        let selectVariant = JSON.parse(selectedFont.variants).find(item => item == productSettings?.Button.varient)
        this.props.BUTTON_CSS_FONT(selectedFont.family)
        this.onChangeButton_verient('change', selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
    };
    onChangeButton_verient = (e, val) => { this.props.BUTTON_FONT_VARIENT(e == 'change' ? val : e.label.key) }

    onChangeButton_fontSize = (e) => { this.props.BUY_FONT_SIZE(parseInt(e)) }

    onChangeProduct_font = (e) => {
        const { allFont, productSettings } = this.props
        const selectedFont = allFont.find(font => font.family == e.label.key)
        let selectVariant = JSON.parse(selectedFont.variants).find(item => item == productSettings?.ProductTitle.varient)
        this.props.TITLE_CSS_FONT(selectedFont.family)
        this.onChangeProduct_verient('change', selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
    };

    onChangeProduct_verient = (e, val) => { this.props.TITLE_FONT_VARIENT(e == 'change' ? val : e.label.key) }

    onChangeProduct_fontSize = (e) => { this.setState({ product_title_font_size: parseInt(e) }); this.props.PRODUCT_TITLE_FONT_SIZE(parseInt(e)) }

    onChangePrice_font = (e) => {
        const { allFont, productSettings } = this.props
        const selectedFont = allFont.find(font => font.family == e.label.key)
        let selectVariant = JSON.parse(selectedFont.variants).find(item => item == productSettings?.Price.varient)
        this.props.PRICE_CSS_FONT(selectedFont.family)
        this.onChangePrice_verient('change', selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
    };

    onChangePrice_verient = (e, val) => { this.props.PRICE_FONT_VARIENT(e == 'change' ? val : e.label.key) }

    onChangePrice_fontSize = (e) => { this.props.ORIGINAL_PRICE_FONT_SIZE(parseInt(e)) }

    onChangeHotspotStatus = () => { this.props.HOTSPOT(this.props.productSettings?.Hotspot?.status == 1 ? 0 : 1) }

    onChangeHotspotType = () => { this.props.HOTSPOT_TYPE(this.props.productSettings?.Hotspot?.type == 1 ? 0 : 1) }

    onChange_tab_target = (e) => { this.props.TAB_TARGET(e.value) }

    onChange_pdp_target = (e) => { this.props.PDP_TARGET(this.props.productSettings?.Button.pdp_target == 1 ? 0 : 1) }

    onSubmitCTA = () => {
        this.props.UPDATE_CTA_ACTION(this.props.productSettings, this.props.wall?.Wall?.id, () => { });
    }

    componentDidUpdate(prevProps) {
        if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
            if (this.props.is_cta_setting_change) { this.onSubmitCTA(); }
        }
    }


    ChangeFeature_text_color = (hex, opacity) => { if (!hex) return; this.props.FEATURE_TEXT_COLOR(hex) }

    onChangeButton_color = (hex, opacity) => { if (!hex) return; this.props.BUY_NOW_COLOR(hex) }

    onChangeButton_background = (hex, opacity) => { if (!hex) return; this.props.BUY_NOW_BACKGROUND(hex) }

    onChangeProduct_color = (hex, opacity) => { if (!hex) return; this.props.PRODUCT_TITLE_COLOR(hex) }

    onChangePrice_color = (hex, opacity) => { if (!hex) return; this.props.ORIGINAL_PRICE_COLOR(hex) }

    onChange_Hotspot_color = (hex, opacity) => { if (!hex) return; this.props.HOTSPOT_COLOR(hex) }

    onChange_Hotspot_type_color = (hex, opacity) => { if (!hex) return; this.props.HOTSPOT_TYPE_COLOR(hex) }

    render() {
        const { inheritStyle, productSettings, is_cta_setting_change } = this.props
        return <Accordion className="border-top" defaultActiveKey="catalog_name" flush>
            <Accordion.Item eventKey="catalog_name">
                <Accordion.Header>Catalog Name</Accordion.Header>
                <Accordion.Body className="bg-gray-100">
                    <CatalogName inheritStyle={inheritStyle} onChangeCatelogFont={this.onChangeCatelogFont} CatalogueTitle={productSettings?.CatalogueTitle} onChangeCatelogVeriant={this.onChangeCatelogVeriant} onChangeFeature_font_size={this.onChangeFeature_font_size} onChangeFeature_text={this.onChangeFeature_text} ChangeFeature_text_color={this.ChangeFeature_text_color} is_cta_setting_change={is_cta_setting_change} />
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="Products_">
                <Accordion.Header>Products</Accordion.Header>
                <Accordion.Body className="bg-gray-100">
                    <Products inheritStyle={inheritStyle} ProductTitle={productSettings?.ProductTitle} Price={productSettings?.Price}
                        onChangeProduct_font={this.onChangeProduct_font} onChangeProduct_verient={this.onChangeProduct_verient} onChangeProduct_fontSize={this.onChangeProduct_fontSize} onChangePrice_font={this.onChangePrice_font} onChangePrice_verient={this.onChangePrice_verient} onChangePrice_fontSize={this.onChangePrice_fontSize} onChangeProduct_color={this.onChangeProduct_color} onChangePrice_color={this.onChangePrice_color} is_cta_setting_change={is_cta_setting_change} />
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="button_">
                <Accordion.Header>Button</Accordion.Header>
                <Accordion.Body className="bg-gray-100">
                    <ProductButton inheritStyle={inheritStyle} ProdButton={productSettings?.Button} onChangeButton_text={this.onChangeButton_text} onChangeButton_font={this.onChangeButton_font} onChangeButton_verient={this.onChangeButton_verient} onChangeButton_fontSize={this.onChangeButton_fontSize} onChange_tab_target={this.onChange_tab_target} onChange_pdp_target={this.onChange_pdp_target} onChangeButton_color={this.onChangeButton_color} onChangeButton_background={this.onChangeButton_background} is_cta_setting_change={is_cta_setting_change} />
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="hotspot_">
                <Accordion.Header>Hotspot</Accordion.Header>
                <Accordion.Body className="bg-gray-100">
                    <Hotspot Hotspot={productSettings?.Hotspot} onChangeHotspotStatus={this.onChangeHotspotStatus} onChangeHotspotType={this.onChangeHotspotType} onChange_Hotspot_color={this.onChange_Hotspot_color} onChange_Hotspot_type_color={this.onChange_Hotspot_type_color} is_cta_setting_change={is_cta_setting_change} />
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
    }
}


const mapStateToProps = (state) => {
    return {
        moderation: state.moderation,
        is_cta_setting_change: state.appData?.is_cta_setting_change,
        save_and_publish: state.appData?.save_and_publish,
        inheritStyle: state.appData?.wall?.ThemeRule?.inheritStyles,
        allFont: state.allFont?.allFonts,
        ctaData: state.ctaData.data,
        productSettings: state.appData?.wall?.ProductSetting,
        wall: state.appData?.wall
    }
}
const mapDispatchToProps = dispatch => {
    return {
        UPDATE_CTA_ACTION: (data, productId, callback) => dispatch(UPDATE_CTA_ACTION(data, productId, callback)),
        FEATURE_TEXT: (data) => dispatch({ type: FEATURE_TEXT, payload: data }),
        FEATURE_TEXT_COLOR: (data) => dispatch({ type: FEATURE_TEXT_COLOR, payload: data }),
        FEATURE_CSS_FONT: (data) => dispatch({ type: FEATURE_CSS_FONT, payload: data }),
        FEATURE_FONT_VARIENT: (data) => dispatch({ type: FEATURE_FONT_VARIENT, payload: data }),
        FEATURE_FONT_SIZE: (data) => dispatch({ type: FEATURE_FONT_SIZE, payload: data }),
        BUY_NOW_TEXT: (data) => dispatch({ type: BUY_NOW_TEXT, payload: data }),
        BUTTON_CSS_FONT: (data) => dispatch({ type: BUTTON_CSS_FONT, payload: data }),
        BUTTON_FONT_VARIENT: (data) => dispatch({ type: BUTTON_FONT_VARIENT, payload: data }),
        BUY_NOW_COLOR: (data) => dispatch({ type: BUY_NOW_COLOR, payload: data }),
        BUY_NOW_BACKGROUND: (data) => dispatch({ type: BUY_NOW_BACKGROUND, payload: data }),
        BUY_FONT_SIZE: (data) => dispatch({ type: BUY_FONT_SIZE, payload: data }),
        PRODUCT_TITLE_COLOR: (data) => dispatch({ type: PRODUCT_TITLE_COLOR, payload: data }),
        PRODUCT_TITLE_FONT_SIZE: (data) => dispatch({ type: PRODUCT_TITLE_FONT_SIZE, payload: data }),
        TITLE_CSS_FONT: (data) => dispatch({ type: TITLE_CSS_FONT, payload: data }),
        TITLE_FONT_VARIENT: (data) => dispatch({ type: TITLE_FONT_VARIENT, payload: data }),
        ORIGINAL_PRICE_COLOR: (data) => dispatch({ type: ORIGINAL_PRICE_COLOR, payload: data }),
        PRICE_CSS_FONT: (data) => dispatch({ type: PRICE_CSS_FONT, payload: data }),
        PRICE_FONT_VARIENT: (data) => dispatch({ type: PRICE_FONT_VARIENT, payload: data }),
        ORIGINAL_PRICE_FONT_SIZE: (data) => dispatch({ type: ORIGINAL_PRICE_FONT_SIZE, payload: data }),
        DISCOUNT_PRICE_COLOR: (data) => dispatch({ type: DISCOUNT_PRICE_COLOR, payload: data }),
        DISCOUNT_CSS_FONT: (data) => dispatch({ type: DISCOUNT_CSS_FONT, payload: data }),
        DISCOUNT_FONT_VARIENT: (data) => dispatch({ type: DISCOUNT_FONT_VARIENT, payload: data }),
        DISCOUNT_PRICE_FONT_SIZE: (data) => dispatch({ type: DISCOUNT_PRICE_FONT_SIZE, payload: data }),
        HOTSPOT: (data) => dispatch({ type: HOTSPOT, payload: data }),
        HOTSPOT_COLOR: (data) => dispatch({ type: HOTSPOT_COLOR, payload: data }),
        HOTSPOT_TYPE: (data) => dispatch({ type: HOTSPOT_TYPE, payload: data }),
        HOTSPOT_TYPE_COLOR: (data) => dispatch({ type: HOTSPOT_TYPE_COLOR, payload: data }),
        STATUS: (data) => dispatch({ type: STATUS, payload: data }),
        TAB_TARGET: (data) => dispatch({ type: TAB_TARGET, payload: data }),
        PDP_TARGET: (data) => dispatch({ type: PDP_TARGET, payload: data })
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CTAButtonStyle);