import React, { Component } from 'react';
import { Row, Col, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import Select from "react-select";

const convertToOption = (data, family) => {
       var options = []
       if(data){
              data.map((item,index) => {
                     options.push({ label: (<span key={family ? item : item.family} style={{ fontFamily: `${family ? family : item.family}` }} className={`sGFfonte-${family ? item : item.family}`} > {family ? item :item.family} </span>), value: family ? index : item.id})
              })
       }
       
       return options
}

class SelectFont extends Component {
       constructor(props){
              super(props)
              this.state = {
                     selectedFont: {},
                     selectedVariant:{},
                     variantList:[],
                     fontList : convertToOption(this.props.allFont)
              }
       }

       componentDidMount(){
              this.handleVarient()
              this.handleFont()
       }

       componentDidUpdate(prevProps){
              if(prevProps.vrntVal != this.props.vrntVal || prevProps.fontval != this.props.fontval){
                     if(prevProps.fontval != this.props.fontval) this.handleFont()
                     this.handleVarient()
              } 
       }

       handleVarient = ()=>{
              const {fontval, vrntVal, allFont} = this.props
              let variants = []
              if(fontval && fontval!='' && fontval!='0'){
                     variants = allFont.find(item => item.family == fontval)
                     if(variants){
                            variants = convertToOption(JSON.parse(variants.variants), fontval)
                            const selected = variants.find(vrnt =>vrnt.label.key == vrntVal)
                            this.setState({variantList: variants, selectedVariant:selected ? selected : variants[0]})
                     }   
              }
              else{
                     variants = convertToOption(JSON.parse(allFont[0].variants), "Default")
                     this.setState({variantList:variants, selectedVariant:variants[0]})
              }              
       }

       handleFont = ()=>{
              const {fontval} = this.props
              const { fontList } = this.state
              if(fontval && fontval!='' && fontval!='0'){
                     let selected = fontList.find(item=> item.label.key == fontval)
                     this.setState({selectedFont: selected})
              }
              else this.setState({selectedFont: fontList[0]})
              
       }

       render() {
              const {inheritStyle, fontChange, variantChange, fontOptn, varaiants, fontval, vrntVal, styles} = this.props
              const {selectedFont, selectedVariant, variantList, fontList} = this.state
              return (
                     <Row className="gx-2">
                            <Col xs={7}>
                                   <Form.Label className="mb-2" htmlFor="font_family">Font Family</Form.Label>
                                   <Select className={`selectpicker selectpicker-sm ${inheritStyle ? "disabled" : ""}`} classNamePrefix="selectpicker" isDisabled={inheritStyle} onChange={fontChange} 
                                   options={fontList} isSearchable={false} value={selectedFont} styles={styles ? styles : ''}/>
                            </Col>
                            <Col xs={5}>
                                   <Form.Label className="mb-2" htmlFor="font_weight">Weight</Form.Label>
                                   <Select className={`selectpicker selectpicker-sm ${inheritStyle ? "disabled" : ""}`} classNamePrefix="selectpicker" isDisabled={inheritStyle} value={selectedVariant} options={variantList} onChange={variantChange} isSearchable={false} styles={styles ? styles : ''}/>
                            </Col>
                     </Row>
              );
       }
}

const mapStateToProps = (state) => {
       return {
           allFont: state.allFont?.allFonts
       }
}

export default connect(mapStateToProps)(SelectFont);