import { Link } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
const SubHeader = ({ websiteUrl }) => {
    return <div className="subheader">
        <Container fluid>
            <div className="nav nav-underline">
                <div className='nav-item'>
                    <Link to={`/website/${websiteUrl}/${localStorage.getItem('website_id') || ''}`} className="nav-link active">
                        <FontAwesomeIcon icon={'browser'} iconClass="me-1" />
                        Website
                    </Link>
                </div>
                <div className='nav-item'>
                    <Link to={`/shopon-bio/preview/${localStorage.getItem('shopOn_id') || ''}`} className="nav-link">
                        <FontAwesomeIcon icon={'link'} iconClass="me-1" />
                        Shopon.bio</Link>
                </div>
                <div className='nav-item'>
                    <Link to={`/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`} className="nav-link">
                        <FontAwesomeIcon icon={'bullhorn'} iconClass="me-1" />
                        Email Campaign</Link>
                </div>
            </div>
        </Container>
    </div>
}

export default SubHeader