import React, { Component } from 'react'
import { OverlayTrigger, Tooltip, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';

export default class ToggleButton extends Component {
    render() {
    const { toggleValue, callbackFn, tooltipTxt , toggleName, callbackFn2, dispatchfn} =  this.props

        return <div className="f-between">
              <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`${toggleName.replace(" ","")}_`}>
                  <span className='me-2'>{toggleName}</span>
                  {tooltipTxt ? <OverlayTrigger placement='top' overlay={<Tooltip>{tooltipTxt}</Tooltip>} >
                      <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-8 text-gray-600'}/></span>
                  </OverlayTrigger> : null}
              </Form.Label>
              <Form.Check type="switch" id={`${toggleName.replace(" ","")}_`} defaultValue={toggleValue} checked={toggleValue==0 ? false : true} onChange={(e) => {callbackFn(e.target.checked);dispatchfn(e.target.checked);callbackFn2(e.target.checked ? false: e.target.checked)}} />
              </div>
    }
}
